import React from "react";
import { Route, Redirect } from "react-router-dom";
const PublicRoute = ({
  component: Component,
  auth,
  ...rest
}) => {
  return (
    <Route
      {...rest}
      render={props => {
        if (auth) {
          return (
            <Redirect
              to={{
                pathname: "/app",
                state: {
                  from: props.location
                }
              }}
            />
          );
        } else {
          return <Component {...props} />;
        }
      }}
    />
  );
};

export default PublicRoute;