import { request } from '../../../shared/verb';
import { setCurrentUser } from '../../actions/user';


export const getCurrentUser = (id) => (dispatch) => {
  return request("user/get?id=" + id, null, 'get').then(({ data }) => {
    dispatch(setCurrentUser(data.data.mongoDB));
  })
}
export const updateUser = (body) => (dispatch) => {
  return request("user/me", body, 'put').then(({ data }) => {
    dispatch(setCurrentUser(data.data.mongoDB));
  })
}

