export const actions = {
  SET_DASHBOARD_STATS: 'SET_DASHBOARD_STATS',
  SET_COMMUNITY_STATS: 'SET_COMMUNITY_STATS',
}

export const setDashboardStats = (data) => ({
  type: actions.SET_DASHBOARD_STATS,
  data,
})
export const setCommunityStats = (data) => ({
  type: actions.SET_COMMUNITY_STATS,
  data,
})