import React, { Component } from 'react'
import { Layout, Menu, Icon, Dropdown, Modal, Input, Button, message, Form, } from 'antd';
import './Main.scss';
// import moment from 'moment';
import logoWide from '../../assets/logoWide.png';
import logo from '../../assets/logo.png';
import { alumni, admin, superAdmin } from "./Menu";
import routes from "./routes";
import { push } from 'connected-react-router';
import { Switch, Route } from 'react-router-dom'
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { logout, verifyUserCode, changePassword, resendUserCode } from '../../redux/services/auth';
const { Header, Sider, Content } = Layout;
const { Item } = Menu;

class Main extends Component {
  state = {
    collapsed: true,
    selected: window.location.pathname,

    //for email verification
    codeVerifying: false,
    verificationStatus: null,
    codeValue: '',
    codeVerificationModal: false,

    //for reset pwd modal
    pwdModal: false,
    changePwdLoader: false,
  };

  toggle = () => {
    this.setState((state) => ({
      collapsed: !state.collapsed,
    }));
  };

  menu = (
    <Menu>
      <Item onClick={() => this.props.push('/app/profile')}>
        Profile
      </Item>
      <Item onClick={() => this.setState({ pwdModal: true })}>
        Reset Password
      </Item>
      <Item onClick={this.props.logout}>
        Log out
      </Item>
    </Menu>
  );

  verifyUserCode = () => {
    if (!this.state.codeValue.trim()) {
      return;
    }
    this.setState({
      codeVerifying: true,
    })
    this.props.verifyUserCode({
      id: this.props.currentUser._id,
      code: +this.state.codeValue,
    }).then(() => {
      message.success('Congratulation! your email has been verified!');
      this.setState({
        codeVerifying: false,
        verificationStatus: 'verified'
      })
      setTimeout(() => {
        window.location.replace('/');
      }, 1500);
    }, () => {
      message.error('Incorrect verification code.');
      this.setState({
        codeVerifying: false,
        verificationStatus: null,
      })
    })
  }

  handleSubmit = e => {
    this.setState({ changePwdLoader: true })
    e.preventDefault();
    this.props.form.validateFields((err, { pwd, newPwd, newPwdAgain }) => {
      if (!err) {
        if (newPwd !== newPwdAgain) {
          message.error("Password does not match!")
          this.setState({ changePwdLoader: false })
          return;
        }
        this.props.changePassword({
          oldPassword: pwd,
          newPassword: newPwd,
          token: localStorage.getItem('accessToken'),
        }).then(() => {
          message.success("Password Updated Successfully!")
          this.setState({ changePwdLoader: false, pwdModal: false, })
        }, () => {
          message.error("Incorrect Password!")
          this.setState({ changePwdLoader: false })
        })
      } else {
        this.setState({ changePwdLoader: false })
      }
    });
  };

  render() {
    let arr = this.props.location.pathname.split('/');
    let key = `/${arr[1]}/${arr[2]}`
    if (arr.length < 3) {
      key = `/${arr[1]}`
    }
    let menuList = [];
    switch (this.props.currentUser.role) {
      case "super-admin":
        menuList = superAdmin;
        break;
      case "admin":
      case "HOD":
        menuList = admin;
        break;
      default:
        menuList = alumni;
    }
    const { getFieldDecorator } = this.props.form;
    return (
      <Layout style={{ height: '100vh' }}>
        <Sider style={{ position: 'relative' }} trigger={null} collapsible collapsed={this.state.collapsed}>
          <div className="logo" style={{ margin: '15px auto', width: 'fit-content' }}>
            <img src={this.state.collapsed ? logo : logoWide} width={this.state.collapsed ? 60 : 150} alt="" />
          </div>
          <Menu
            style={{ marginTop: 50 }}
            theme="dark"
            mode="inline"
            defaultSelectedKeys={[key]}
            selectedKeys={[key]}
            onSelect={({ key }) => {
              this.props.push(key);
              this.setState({ selected: key })
            }}
          >
            {
              menuList.map((m) => (
                <Item key={m.link}>
                  {m.icon}
                  <span>{m.title}</span>
                </Item>
              ))
            }
          </Menu>
          <span
            style={{
              position: 'absolute',
              bottom: 5,
              left: 0,
              fontSize: 12,
              color: '#ffffff68',
              textAlign: 'center',
              width: '100%',
            }}
          >Powered by Cyphersofts</span>
        </Sider>
        <Layout>
          <Header
            style={{
              background: '#fff',
              padding: 0,
              display: 'flex',
              justifyContent: 'space-between',
              alignItems: 'center',
            }}
          >
            <Icon
              style={{ margin: 25, fontSize: 15 }}
              className="trigger"
              type={this.state.collapsed ? 'menu-unfold' : 'menu-fold'}
              onClick={this.toggle}
            />

            {
              !this.props.currentUser.verified ?
                <div
                  style={{
                    padding: '0px 20px',
                    color: '#d13838',
                    fontWeight: 'bold',
                    background: '#d1383826',
                    letterSpacing: '0.2px'
                  }}
                >
                  Your email is not verified.
                  <span
                    style={{
                      marginLeft: 10,
                      color: '#000',
                      cursor: 'pointer',
                      textDecoration: 'underline'
                    }}
                    onClick={() => this.setState({ codeVerificationModal: true })}
                  >Verify now!</span>
                </div>
                : null
            }

            {this.props.currentUser ?
              <Dropdown overlay={this.menu} placement="bottomRight">
                <div style={{ lineHeight: 'initial', marginRight: 25, cursor: 'pointer' }}>
                  <div
                    style={{
                      fontSize: 16,
                      fontWeight: 'bold',
                      textTransform: 'capitalize'
                    }}
                  >{this.props.currentUser.username} ({this.props.currentUser.role}) &#9662;</div>
                  <span style={{ fontSize: 12 }}>{this.props.currentUser.email}</span><br />
                </div>
              </Dropdown> : null
            }
          </Header>
          <Content>
            <Switch>
              {
                routes.map((r, i) => (
                  <Route key={i} {...r} />
                ))
              }
            </Switch>
          </Content>
        </Layout>
        <Modal
          title="Email Verification"
          visible={this.state.codeVerificationModal}
          destroyOnClose
          onCancel={() => this.setState({ codeVerificationModal: false })}
          footer={null}
          centered
        >
          <div style={{ fontWeight: 'bold', textAlign: 'center' }}>Check your email ({this.props.currentUser.email}) for verification code.</div>
          <div style={{ maxWidth: 350, margin: '20px auto', display: 'flex' }}>
            <Input
              disabled={this.state.verifyingCode}
              value={this.state.codeValue}
              onChange={e => this.setState({ codeValue: e.target.value })}
              placeholder="Enter Verification Code"
              type='text'
              onPressEnter={this.verifyUserCode}
            />
            <Button
              onClick={this.verifyUserCode}
              disabled={this.state.verificationStatus === 'verified' || this.state.codeVerifying}
              type="primary"
              shape="circle"
              icon={
                !this.state.verificationStatus ?
                  this.state.codeVerifying ? "loading" : "arrow-right"
                  : this.state.verificationStatus === 'verified' ?
                    "check"
                    : "arrow-right"
              }
              style={{
                width: 35,
                marginLeft: 10,
                background: this.state.verificationStatus === 'verified' ? 'green' : undefined,
              }}
            />
          </div>
          <Button
            style={{
              margin: 'auto',
              display: 'block',
              width: 'fit-content',
            }}
            disabled={this.state.resendTimer}
            loading={this.state.resendEmailLoader}
            onClick={() => {
              this.setState({
                resendEmailLoader: true,
              })

              this.props.resendUserCode({
                id: this.props.currentUser._id
              }).then(() => {
                message.success("Email sent successfully!")
                this.setState({
                  resendEmailLoader: false,
                })
              })
            }}
          >
            Resend Email <Icon type='arrow-right' /></Button>
        </Modal>
        <Modal
          title="Reset Password"
          visible={this.state.pwdModal}
          onCancel={() => {
            this.setState({ pwdModal: false })
          }}
          onOk={this.handleSubmit}
          okButtonProps={{
            loading: this.state.changePwdLoader,
          }}
          destroyOnClose
          centered
        >
          <Form labelCol={{ span: 10 }} wrapperCol={{ span: 12 }}>
            <Form.Item label="Current Password">
              {getFieldDecorator('pwd', {
                rules: [{ required: true, message: 'Current Password is required!' }],
              })(<Input type='password' />)}
            </Form.Item>
            <Form.Item label="New Password">
              {getFieldDecorator('newPwd', {
                rules: [{
                  required: true,
                  message: 'Password must be at least 8 character',
                  min: 8,
                  max: 16,
                }],
              })(<Input type='password' />)}
            </Form.Item>
            <Form.Item label="New Password Again">
              {getFieldDecorator('newPwdAgain', {
                rules: [{
                  required: true, message: 'Password must be at least 8 character', min: 8,
                  max: 16,
                }],
              })(<Input type='password' />)}
            </Form.Item>
          </Form>
        </Modal>
      </Layout >
    );
  }
}

const mapStateToProps = (state) => ({
  currentUser: state.auth.user,
  location: state.router.location,
})

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    push,
    verifyUserCode,
    resendUserCode,
    changePassword,
    logout
  }, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(Form.create({ name: "reset-pwd" })(Main));