import { actions } from "../../actions/alumni";

const initialState = {
  allAlumni: [],
  pendingAlumni: [],
}

export default (state = initialState, { type, data }) => {
  switch (type) {
    case actions.SET_ALL_ALUMNI:
      return { ...state, allAlumni: data }
    case actions.SET_PENDING_ALUMNI:
      return { ...state, pendingAlumni: data }
    case 'logout':
      return { ...initialState }
    default:
      return state
  }
}