import React, { Component } from 'react'
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import styles from './UserProfile.module.scss'
import { Avatar, Button, Icon, Tag, Spin, Modal, Form, Input, Select } from 'antd';
import { push } from 'connected-react-router';
import { getCurrentUser, updateUser } from '../../redux/services/user'
import { getAllDepartments } from '../../redux/services/departments'
import moment from 'moment';
import ReactToPrint from 'react-to-print';

function InfoItem({ title, value }) {
  return (
    <div>
      <span className='title' style={{ flex: 1, color: '#001529', }}>{title}:</span>
      <span className='value' >{value || <>&ndash;</>}</span>
    </div>
  )
}

class UserProfile extends Component {

  state = {
    loading: true,
    currId: null,
    editModal: false,
    editing: false,
  }

  componentDidMount() {
    let id = this.props.location.pathname.split('/')[3];
    this.setState({
      currId: id,
    })
    this.props.getCurrentUser(id || this.props.user._id).then(() => {
      this.setState({
        loading: false
      })
    }, () => {
      this.props.push('/app/profile')
    })

    if (!this.props.departs.length) this.props.getAllDepartments();
  }

  componentDidUpdate() {
    let id = this.props.location.pathname.split('/')[3];
    if (this.state.currId !== id) {
      this.setState({
        loading: true,
        currId: id,
      })
      this.props.getCurrentUser(id || this.props.user._id).then(() => {
        this.setState({
          loading: false
        })
      }, () => {
        this.props.push('/app/profile')
      })
    }
  }

  handleSubmit = e => {
    this.setState({ editing: true })
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        this.props.updateUser(values).then(() => {
          this.setState({ editing: false, editModal: false })
        })
      } else {
        this.setState({ editing: false })
      }
    });
  };

  render() {
    if (this.state.loading) {
      return (
        <div style={{
          width: '100%',
          height: '100%',
          display: 'flex',
          flexDirection: 'column',
          justifyContent: 'center'
        }}>
          <Spin />
        </div>
      )
    }

    const { getFieldDecorator } = this.props.form;

    let data = {};
    if (this.props.currentUser) {
      data = { ...this.props.currentUser }
    }
    const {
      username,
      email,
      phone,
      enrollmentNo,
      nicNo,
      bio,
      skills,
      DOB,
      address,
      gender,
      role,
      status,
      createdAt,
      department,
      degree,
      company,
      workingExperience,
      jobTitle,
      employmentStatus
    } = data;
    return (
      <div ref={el => (this.componentRef = el)} className={styles.alumniProfile}>
        <div className={styles.top}>
          <div className={styles.profileBanner}>
            <div className={styles.profileImg}>
              <Avatar src='https://picsum.photos/100' size={100} icon="user" shape='square' />
            </div>
            <div className={styles.profileTopData}>
              <h2 style={{ color: '#001529', textTransform: 'capitalize' }}>
                {username} ({role})
                {
                  role === 'alumni' ?
                    <Tag className="no-print" style={{ marginLeft: 10 }}>
                      {status}
                    </Tag>
                    : null
                }
              </h2>
              <span>{email}</span>
              <p>
                {bio}
              </p>
              <div>
                {
                  skills ? skills.map((s, i) => (
                    <Tag key={i}>{s}</Tag>
                  )) : null
                }
              </div>
            </div>
          </div>
          <ReactToPrint
            trigger={() => (
              <Button className={styles.backBtn + ' no-print'} type="secondary" >
                <Icon type="download" />
                Print Resume
                </Button>
            )}
            content={() => this.componentRef}
          />

        </div>
        <div className={styles.personalDetails}>
          <div style={{
            background: '#00000024',
            fontWeight: 'bolder',
            display: 'flex',
            justifyContent: 'space-between',
            alignItems: 'center',
            padding: '6px 12px',
          }}>
            <h2>Personal Details</h2>
            {
              this.props.user._id === this.props.currentUser._id ?
                <Button type="ghost" icon="edit" className="no-print" onClick={() => { this.setState({ editModal: true }) }} />
                : null
            }
          </div>
          <div className={styles.info}>
            {this.props.user._id === this.props.currentUser._id || this.props.user.role !== 'alumni' ?
              <div className={styles.infoItems}>
                <h3>Basic Info</h3>
                <InfoItem title='Birth Date' value={moment(DOB).format('MMM DD, YYYY')} />
                <InfoItem title='Gender' value={gender} />
                <InfoItem title='Contact' value={phone} />
                <InfoItem title='NIC no' value={nicNo} />
                <InfoItem title='Address' value={address} />
                <InfoItem title='Registered At' value={moment(createdAt).format('MMM DD, YYYY h:mm a')} />
              </div>
              : null
            }
            <div className={styles.infoItems}>
              <h3>Work Experience</h3>
              <InfoItem title='Job Position' value={jobTitle} />
              <InfoItem title='Company' value={company} />
              <InfoItem title='Experience in relevant field' value={workingExperience} />
            </div>
            <div className={styles.infoItems}>
              <h3>Education</h3>
              <InfoItem title='Enrollment No' value={enrollmentNo} />
              <InfoItem title='Department' value={department ? `${department.initials}-${department.name}`.toUpperCase() : undefined} />
              <InfoItem title='Degree' value={degree} />
            </div>
          </div>
        </div>
        <Modal
          title="Edit Profile"
          visible={this.state.editModal}
          onCancel={() => {
            this.setState({ editModal: false })
          }}
          okText="Save"
          onOk={this.handleSubmit}
          okButtonProps={{
            loading: this.state.editing,
          }}
          destroyOnClose
          centered
        >
          <Form labelCol={{ span: 7 }} wrapperCol={{ span: 12 }} onSubmit={this.handleSubmit}>
            <Form.Item label="Bio">
              {getFieldDecorator('bio', {
                initialValue: bio
              })(<Input.TextArea type='text' />)}
            </Form.Item>
            <Form.Item label="Contact">
              {getFieldDecorator('phone', {
                initialValue: phone
              })(<Input type='number' />)}
            </Form.Item>
            <Form.Item label="Address">
              {getFieldDecorator('address', {
                initialValue: address
              })(<Input type='text' />)}
            </Form.Item>
            <Form.Item label="Gender">
              {getFieldDecorator('gender', {
                initialValue: gender
              })(<Select>
                <Select.Option value='male'>Male</Select.Option>
                <Select.Option value='female'>Female</Select.Option>
                <Select.Option value='other'>Other</Select.Option>
              </Select>)}
            </Form.Item>
            <Form.Item label="Employment Status">
              {getFieldDecorator('employmentStatus', {
                initialValue: employmentStatus
              })(<Select>
                <Select.Option value='Employed'>Employed</Select.Option>
                <Select.Option value='Part-Time'>Part-Time</Select.Option>
                <Select.Option value='Self-Employed'>Self-Employed</Select.Option>
                <Select.Option value='Unemployed'>Unemployed</Select.Option>
              </Select>)}
            </Form.Item>
            <Form.Item label="Skills">
              {getFieldDecorator('skills', {
                initialValue: skills
              })(<Select dropdownStyle={{ display: 'none' }} mode='tags' maxTagCount={5}></Select>)}
            </Form.Item>
            <Form.Item label="Experience">
              {getFieldDecorator('workingExperience', {
                initialValue: workingExperience
              })(<Input type='text' />)}
            </Form.Item>
            <Form.Item label="Company">
              {getFieldDecorator('company', {
                initialValue: company
              })(<Input type='text' />)}
            </Form.Item>
            <Form.Item label="Job Position">
              {getFieldDecorator('jobTitle', {
                initialValue: jobTitle
              })(<Input type='text' />)}
            </Form.Item>
            <Form.Item label="Degree">
              {getFieldDecorator('degree', {
                initialValue: degree
              })(<Input type='text' />)}
            </Form.Item>
            <Form.Item label="Department">
              {getFieldDecorator('department', {
                initialValue: department ? department._id : null
              })(<Select>
                {
                  this.props.departs.map(d => (
                    <Select.Option
                      key={d._id}
                      value={d._id}
                    >
                      {`${d.initials}-${d.name}`}
                    </Select.Option>
                  ))
                }
              </Select>)}
            </Form.Item>
          </Form>
        </Modal>
      </div>
    )
  }
}

const mapStateToProps = state => ({
  currentUser: state.user.currentUser,
  user: state.auth.user,
  location: state.router.location,
  departs: state.departments.allDepartments,
})

const mapDispatchToProps = dispatch => {
  return bindActionCreators({
    push, updateUser,
    getCurrentUser,
    getAllDepartments
  }, dispatch)
}

export default connect(mapStateToProps, mapDispatchToProps)(Form.create({ name: 'editProfile' })(UserProfile));