import api from './api';
import axios from 'axios';
import createAuthRefreshInterceptor from 'axios-auth-refresh';

const fetchToken = () => localStorage.getItem('token');


// Function that will be called to refresh authorization
const refreshAuthLogic = failedRequest => axios.put(`${api}/user/refresh`, {
  "refreshToken": localStorage.getItem('refreshToken')
}).then(tokenRefreshResponse => {
  localStorage.setItem('token', tokenRefreshResponse.data.data.IdToken);
  failedRequest.response.config.headers['Authorization'] = tokenRefreshResponse.data.data.IdToken;
  return Promise.resolve();
});

// Instantiate the interceptor (you can chain it as it returns the axios instance)
const interceppedAxios = createAuthRefreshInterceptor(axios, refreshAuthLogic, {
  statusCodes: [500]
});




export const request = (url, data, type, noHeaders, params) => new Promise((resolve, reject) => {
  let token = '';
  const request = {
    'method': type,
    'url': api + url
  };
  noHeaders || (token = fetchToken());
  type !== 'get' && (request.data = data);
  noHeaders || (request.headers = { 'Authorization': token });
  params && (request.params = params);
  interceppedAxios(request).then(resolve, reject);
});