import React, { Component } from 'react'
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import styles from "./HeadOfDepartments.module.scss"; //using scss modules here..
import { Table, Spin, Button, Input, Modal, Form, Select, DatePicker } from 'antd';
import { getAllHOD, createHOD } from '../../redux/services/HOD';
import moment from 'moment'
import { push } from 'connected-react-router'


const { Search } = Input;
const { Option } = Select
const FormItem = Form.Item;

const formItemLayout = {
  labelCol: {
    span: 6
  },
  wrapperCol: {
    span: 14
  }
};

const columns = [
  {
    title: 'Name',
    dataIndex: 'username',
    width: 350,
  },
  {
    title: 'Email',
    dataIndex: 'email',
    width: 350,
  },
  {
    title: 'Nic',
    dataIndex: 'nicNo',
    width: 250,
  },
  {
    title: 'Contact',
    dataIndex: 'phone',
    width: 200,
  },

  {
    title: 'Gender',
    dataIndex: 'gender',

  },
];

const TableHeading = ({ text, modalAction }) => (
  <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', padding: '0 15px' }}>
    <h2 style={{ margin: 0, }}>{text}</h2>

    <div>
      <Button icon='plus' style={{ marginRight: 10 }} type='primary' onClick={() => modalAction('open')}>Add Admin</Button>
      <Search
        placeholder="Search"
        onSearch={value => console.log(value)}
        style={{ width: 200 }}
      />
    </div>

  </div>
)

class HOD extends Component {
  static FormInput = ({ label, initialValue, name, message, required, isDisabled, getFieldDecorator, rules, maxLength, minLength }) => {
    return (
      <FormItem label={label}  {...formItemLayout}>
        {getFieldDecorator(name, {
          initialValue,
          rules: [
            {
              ...rules,
              len: maxLength ? maxLength : null,
              min: minLength ? minLength : null,
              required,
              message,
            }
          ]
        })(<Input disabled={isDisabled} />)}
      </FormItem>
    )
  }
  static FormDropDown = ({ label, initialValue, name, message, required, isDisabled, getFieldDecorator, renderOptions, onChange }) => {
    return (
      <FormItem label={label}  {...formItemLayout}>
        {getFieldDecorator(name, {
          initialValue,
          rules: [
            {
              required,
              message
            }
          ]
        })(<Select onChange={onChange ? (id) => onChange(id) : null} getPopupContainer={trigger => trigger.parentNode} disabled={isDisabled} >{renderOptions()}</Select>)}
      </FormItem>
    )
  }

  static DatePicker = ({ label, initialValue, name, message, required, isDisabled, getFieldDecorator, renderOptions, onChange }) => {
    return (
      <FormItem label={label}  {...formItemLayout}>
        {getFieldDecorator(name, {
          initialValue,
          rules: [
            {
              required,
              message
            }
          ]
        })(<DatePicker
          format='DD/MM/YYYY'
        />)}
      </FormItem>
    )
  }


  state = {
    tableHeight: 500,
    loading: true,
    spiner: false
  }

  componentDidMount() {
    if (this.props.user.role === 'alumni') {
      this.props.push('/app/community');
    } else if (this.props.user.role === 'admin' || this.props.user.role === 'HOD') {
      this.props.push('/app');
    } else {
      this.resizeTableHandler();
      this.getData()
      window.addEventListener('resize', this.resizeTableHandler); // Subcsription to handle table size on resize
    }
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.resizeTableHandler);
  }

  getData = async () => {
    this.setState({ loading: true })
    await this.props.getAllHOD()
    this.setState({ loading: false })
  }

  resizeTableHandler = () => {
    this.setState({
      tableHeight: window.innerHeight - 180,
    })
  }
  modalAction = (action) => {
    if (action === 'close') {
      this.setState({ modalOpen: false })
    } else {
      this.setState({ modalOpen: true })
    }
  }

  renderGender = () => {
    const renderOptions = [<Option value={'male'} key={Date.now()}>Male</Option>, <Option value={'female'} key={Date.now()}>Female</Option>, <Option value={'other'} key={Date.now()}>Other</Option>,]
    return renderOptions
  }
  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        const data = { ...values }
        data.DOB = moment(data.DOB).format('YYYY-MM-DD')
        data.role = 'admin'
        this.setState({ spiner: true })
        this.props.createHOD(data, this.props.allHOD).then(() => this.setState({ spiner: false }, () => this.modalAction('close')))

      }
    });
  };



  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Spin spinning={this.state.loading}>
        <div className={styles.container}>
          <Modal title='Add Admin' visible={this.state.modalOpen} onOk={this.handleSubmit} onCancel={() => this.modalAction('close')}>
            <Spin spinning={this.state.spiner}>
              <Form horizontal>
                <HOD.FormInput
                  label={'Name: '} name="username" initialValue={''}
                  getFieldDecorator={getFieldDecorator} message="Required" required={true}
                  isDisabled={false}
                />
                <HOD.FormInput
                  label={'Email: '} name="email" initialValue={''} rules={{
                    type: 'email',
                    message: 'The input is not valid E-mail!',
                  }}
                  getFieldDecorator={getFieldDecorator} message="Enter valid email address" required={true}
                  isDisabled={false}
                />
                <HOD.FormInput
                  label={'nicNo: '} name="nicNo" initialValue={''}
                  getFieldDecorator={getFieldDecorator} message="Enter 13 digit nic no." required={true}
                  isDisabled={false} maxLength={13} type={'number'}
                />
                <HOD.FormInput
                  label={'Temp Pwd: '} name="password" initialValue={''}
                  getFieldDecorator={getFieldDecorator} message="Enter 8 characters password." required={true}
                  isDisabled={false} minLength={8} type={'number'}
                />
                <HOD.FormDropDown
                  label={'Gender: '} name="gender" initialValue={''}
                  getFieldDecorator={getFieldDecorator} message="Required" required={true}
                  isDisabled={false} renderOptions={this.renderGender}
                />
                <HOD.DatePicker
                  label={'DOB: '} name="DOB" initialValue={''}
                  getFieldDecorator={getFieldDecorator} message="Required" required={true}
                  isDisabled={false} />

              </Form>
            </Spin>

          </Modal>
          <Table
            title={() => <TableHeading text="Admins" modalAction={this.modalAction} />}
            columns={columns}
            dataSource={this.props.allHOD}
            size="middle"
            pagination={false}
            scroll={{ y: this.state.tableHeight - 90 }}
            style={{
              height: this.state.tableHeight,
            }}
            bordered
          />
        </div>
      </Spin>
    )
  }
}


//Code below is used to connect this component with the redux store.. you can ignore it.. for now! ;)

//this function is use.. to get data from REDUX store..
const mapStateToProps = state => ({
  user: state.auth.user,
  allHOD: state.hod.allHOD,
})

//this function is used to provide functions to update data in REDUX store..
const mapDispatchToProps = dispatch => {
  return bindActionCreators({
    getAllHOD,
    createHOD,
    push
  }, dispatch)
}

//Both functions above are connected to the component using "connect" function from "react-redux" package.
export default connect(mapStateToProps, mapDispatchToProps)(Form.create()(HOD));