import { actions } from '../../actions/auth';

const INITIAL_STATE = {
  token: '',
  user: null,
  authenticated: 'undecided',
};

export default function reducer(state = INITIAL_STATE, { data, type }) {
  switch (type) {
    case actions.SET_USER: {
      return { ...state, user: data };
    }
    case actions.SET_USER_TOKEN: {
      return { ...state, token: data };
    }
    case actions.SET_AUTH_STATUS: {
      return { ...state, authenticated: data };
    }
    case "LOGOUT": {
      return { ...INITIAL_STATE, authenticated: false };
    }
    default:
      return state;
  }
}