import React, { Component } from 'react'
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import styles from "./Alumni.module.scss"; //using scss modules here..
import { Table, Input, Icon, Button, Tag } from 'antd';
import { getAllAlumni } from '../../redux/services/alumni';
import { push } from 'connected-react-router';

const { Search } = Input;

function ucFirst(string = ' ') {
  return string.charAt(0).toUpperCase() + string.slice(1);
}

const columns = [
  {
    title: 'Name',
    key: 'Name',
    width: 180,
    render: rowData => (
      <span
        style={{
          marginLeft: 10
        }}
      >{ucFirst(rowData.username)}</span>
    )
  },
  {
    title: 'Gender',
    key: 'Gender',
    align: 'center',
    width: 90,
    filters: [
      {
        text: 'Male',
        value: 'male',
      },
      {
        text: 'Female',
        value: 'female',
      }
    ],
    render: rowData => (
      <span >{ucFirst(rowData.gender)}</span>
    ),
    onFilter: (value, record) => { return record.gender === value },
  },
  {
    title: 'Skills',
    key: 'Skills',
    width: 120,
    align: 'center',
    render: rowData => (
      <span>
        {
          rowData.skills.length ?
            rowData.skills.map((s, i) => (
              <Tag key={i} style={{ marginBottom: '5px' }}>{s}</Tag>
            ))
            :
            <Tag>None</Tag>
        }
      </span>
    )
  },
  {
    title: 'Job Status',
    key: 'Job Status',
    align: 'center',
    width: 120,
    dataIndex: 'employmentStatus',
  },
  {
    title: 'Company',
    key: 'Company',
    align: 'center',
    width: 120,
    dataIndex: 'company',
  },
  {
    title: 'Contact',
    key: 'Contact',
    align: 'center',
    dataIndex: 'phone',
    width: 120,
  },
  {
    title: 'Email',
    key: 'Email',
    width: 200,
    render: rowData => (
      <span onClick={e => e.stopPropagation()}>
        <a href={`mailto:${rowData.email}`}>{rowData.email}</a>&nbsp;&nbsp;
        <Icon type="link" />
      </span>
    )
  },
  {
    title: 'Address',
    key: 'Address',
    width: 200,
    dataIndex: 'address',
  },
];

const TableHeading = ({ text, onRefresh, onSearch }) => (
  <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', padding: '0 15px' }}>
    <h2 style={{ margin: 0 }}>{text}</h2>
    <div style={{ display: 'flex', alignItems: 'center' }}>
      <Button
        shape="circle"
        icon="sync"
        size='small'
        onClick={onRefresh}
        style={{
          marginRight: 10,
        }}
      />
      <Search
        placeholder="Search"
        onSearch={onSearch}
        style={{ width: 200 }}
      />
    </div>
  </div>
)
class Alumni extends Component {

  state = {
    tableHeight: 500,
    loading: true,
    searchKey: ''
  }

  componentDidMount() {
    this.resizeTableHandler();
    if (this.props.allAlumni.length) {
      this.setState({
        loading: false
      })
      this.props.getAllAlumni();
    } else {
      this.props.getAllAlumni().then(() => {
        this.setState({
          loading: false
        })
      })
    }
    window.addEventListener('resize', this.resizeTableHandler); // Subcsription to handle table size on resize

  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.resizeTableHandler);
  }

  resizeTableHandler = () => {
    this.setState({
      tableHeight: window.innerHeight - 180,
    })
  }

  refresh = () => {
    this.setState({
      loading: true
    })
    this.props.getAllAlumni().then(() => {
      this.setState({
        loading: false
      })
    })
  }

  filterIt() {
    const searchKey = this.state.searchKey;
    if (!searchKey.trim()) {
      return [...this.props.allAlumni.filter(a => a.status === "verified")]
    }
    return this.props.allAlumni.filter(a => a.status === "verified").filter(obj => Object.keys(obj).some((key) =>
      (
        (key + "" !== 'key') && (key + "" !== 'updatedAt') && (key + "" !== 'createdAt') &&
        (key + "" !== 'description') && (key + "" !== '_id')) ? ((obj[key] + "").toLowerCase()).includes(searchKey.toLowerCase()) : null
    ));
  }

  render() {
    return (
      <div className={styles.container}>
        <Table
          loading={this.state.loading}
          title={() => (<TableHeading
            onSearch={value => this.setState({ searchKey: value })}
            onRefresh={this.refresh}
            text="Alumni" />)}
          columns={columns}
          onRow={(record) => ({
            onClick: () => this.props.push('/app/profile/' + record._id), // click row
          })}
          rowKey={row => row._id}
          dataSource={this.filterIt()}
          size="middle"
          bordered
          pagination={{
            hideOnSinglePage: true,
          }}
          scroll={{ y: this.state.tableHeight - 90, x: 1150 }}
          bodyStyle={{
            height: this.state.tableHeight - 30,
          }}
        />
      </div>
    )
  }
}


//Code below is used to connect this component with the redux store.. you can ignore it.. for now! ;)

//this function is use.. to get data from REDUX store..
const mapStateToProps = state => ({
  allAlumni: state.alumni.allAlumni,
})

//this function is used to provide functions to update data in REDUX store..
const mapDispatchToProps = dispatch => {
  return bindActionCreators({
    getAllAlumni,
    push
  }, dispatch)
}

//Both functions above are connected to the component using "connect" function from "react-redux" package.
export default connect(mapStateToProps, mapDispatchToProps)(Alumni);