import React, { Component } from 'react'
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import styles from './Dashboard.module.scss'
import { Card, Row, Col } from 'antd';
import alumniIcon from '../../assets/alumni.svg'
import adminIcon from '../../assets/admin.svg'
import departmentIcon from '../../assets/department.svg'
import facultyIcon from '../../assets/faculty.svg'
import CountUp from 'react-countup';
import { getDashboardStats, getCommunityStats } from '../../redux/services/dashboard';
import { push } from 'connected-react-router'
import ReactApexChart from 'react-apexcharts'
const cards = [
  {
    title: 'Alumni',
    key: 'alumni',
    icon: alumniIcon,
  },
  {
    title: 'Department',
    key: 'departs',
    icon: departmentIcon,
  },
  {
    title: 'Admin',
    key: 'admins',
    icon: adminIcon,
  },
  {
    title: 'Community Posts',
    key: 'postsCount',
    icon: facultyIcon,
  },

]

const DataCard = React.memo(({ icon, title, value }) => (
  <Card
    bodyStyle={{
      padding: 15,
    }}
  >
    <div
      style={{
        display: 'flex',
        justifyContent: 'space-between'
      }}
    >
      <img width={60} src={icon} alt="" />
      <div style={{ textAlign: 'right' }}>
        <h2>{title}</h2>
        <span style={{ fontSize: 28 }}><CountUp start={0} end={value} /></span>
      </div>
    </div>
  </Card>
))

class Dashboard extends Component {
  state = {
    options: {
      labels: ['Un-Employed', 'Employed', 'Part-Time', 'Self-Employed'],
      responsive: [{
        breakpoint: 480,
        options: {
          chart: {
            width: 300,

          },
          legend: {
            position: 'bottom'
          }
        }
      }]
    },
    series: [44, 55, 13, 43, 22],
    timeSeriesOptions: {
      chart: {
        stacked: false,
        zoom: {
          enabled: false,
        },
        toolbar: {
          show: false,
        }
      },
      plotOptions: {
        line: {
          curve: 'smooth',
        }
      },
      dataLabels: {
        enabled: false
      },
      markers: {
        size: 0,
        style: 'full',
      },
      //colors: ['#0165fc'],
      title: {
        text: 'Community Posts Trends (Last 30 Days)',
        align: 'left'
      },
      fill: {
        type: 'gradient',
        gradient: {
          shadeIntensity: 1,
          inverseColors: false,
          opacityFrom: 0.5,
          opacityTo: 0,
          stops: [0, 90, 100]
        },
      },
      yaxis: {
        min: 0,
        labels: {
          formatter: function (val) {
            return val.toFixed(0);
          },
        },
        title: {
          text: 'Community Posts'
        },
      },
      xaxis: {
        type: 'datetime',
      },
      tooltip: {
        shared: false,
        y: {
          formatter: function (val) {
            return (val / 1000000).toFixed(0)
          }
        }
      }
    },
  }

  componentDidMount() {
    if (this.props.user.role === 'alumni') {
      this.props.push('/app/community');
    } else {
      this.props.getDashboardStats();
      this.props.getCommunityStats();
    }
  }

  render() {
    console.log(this.props.communityStats)
    let series = [];
    if (this.props.stats.employmentStatus) {
      const { employed, partTime, selfEmployed, unEmployed } = this.props.stats.employmentStatus;
      series = [unEmployed, employed, partTime, selfEmployed];
    }
    return (
      <div className={styles.container}>
        <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
          {
            cards.map((c, i) => (
              <Col key={i} span={6} xs={24} sm={12} md={12} lg={6} >
                <DataCard title={c.title} value={this.props.stats[c.key]} icon={c.icon} />
              </Col>
            ))
          }
        </Row>
        <Row style={{ padding: 20 }} gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
          <Col span={6} md={24} lg={10} >
            <div
              style={{
                margin: 40,
                textAlign: 'center',
              }}
            >
              <h3 style={{ fontWeight: 'bold' }}>Employment Status</h3>
              <br />
              <div>
                <div id="chart">
                  <ReactApexChart options={this.state.options} series={series} type="pie" width="380" />
                </div>
              </div>
            </div>
          </Col>
          <Col span={6} md={24} lg={14} >
            <div id="anotherChart">
              <ReactApexChart options={this.state.timeSeriesOptions} series={[{
                name: 'Posts',
                data: this.props.communityStats,
              }]} type="area" height="350" />
            </div>
          </Col>
        </Row>
      </div>
    )
  }
}


//these functions below are used for redux only!!!

//this is used to get data from redux
const mapStateToProps = state => ({
  stats: state.dashboard.stats,
  user: state.auth.user,
  communityStats: state.dashboard.communityStats,
})

//this is used to send/update data to redux
const mapDispatchToProps = dispatch => {
  return bindActionCreators({
    getDashboardStats,
    getCommunityStats,
    push
  }, dispatch)
}


export default connect(mapStateToProps, mapDispatchToProps)(Dashboard);