import React, { Component } from 'react';
import Login from './container/Login/Login';
import 'antd/dist/antd.css'
import './App.css';
import { Switch } from 'react-router-dom';
import ProtectedRoute from './component/ProtectedRoute/ProtectedRoute.js';
import Register from './container/Register/Register';
import PublicRoute from './component/PublicRoute/PublicRoute';
import Main from './container/Main/Main';
import { request } from './shared/verb';
import logo from './assets/logoB.png';
import { connect } from 'react-redux';
import { logout } from './redux/services/auth';
import { setUserData, setAuthStatus } from './redux/actions/auth';
import { bindActionCreators } from 'redux';

class App extends Component {
  state = {
    authLoader: true,
  }

  verifyMe = () => {
    request('user/me', null, 'get')
      .then(({ data }) => {
        this.props.setUserData(data.data.mongoDB[0]);
        this.setState({ authLoader: false })
        this.signIn();
      }, () => {
        this.signOut();
        this.setState({ authLoader: false })
      })
  }

  componentDidMount() {
    this.verifyMe();
    // this.setState({ authLoader: false })
    // this.props.setAuthStatus(true);

  }

  NoPageFound = () => <h1>No Page Found</h1>

  signIn = () => {
    this.props.setAuthStatus(true);
  }
  signOut = () => {
    this.props.logout()
  }

  render() {
    return (
      this.state.authLoader || this.props.isAuth === 'undecided' ?
        <div className='fullscreenLoader'>
          <div>
            <img src={logo} alt="" width={200} />
            <div style={{ display: 'flex', justifyContent: 'center', margin: 30 }}>
              <div className="dot-loader"></div>
              <div className="dot-loader"></div>
              <div className="dot-loader"></div>
            </div>
          </div>
        </div>
        :
        <Switch>
          <PublicRoute auth={this.props.isAuth} exact path='/' component={() => <Login signedIn={this.signIn} />} />
          <PublicRoute auth={this.props.isAuth} exact path='/register' component={Register} />
          <ProtectedRoute auth={this.props.isAuth} path='/app' component={() => <Main />} />
          <ProtectedRoute auth={this.props.isAuth} path='*' component={this.NoPageFound} />
        </Switch>
    );
  }
}

const mapStateToProps = (state) => ({
  isAuth: state.auth.authenticated,
})

const mapDispatchToProps = (dispatch) => (
  bindActionCreators({
    logout,
    setAuthStatus,
    setUserData
  }, dispatch)
)


export default connect(mapStateToProps, mapDispatchToProps)(App);
