import React, { Component } from 'react'
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import styles from './Community.module.scss'
import PostValue from '../../component/PostValue/PostValue';
import Post from '../../component/Post/Post';
import { Button, Spin, Tabs, Modal, message } from 'antd'
import smoothscroll from 'smoothscroll-polyfill';
import CommentDrawer from '../../component/CommentDrawer/CommentDrawer';
import { getAllPosts, createPost, updatePost, deletePost, getAllBroadcastedPosts } from '../../redux/services/community';
import { setAllPosts, setAllBroadcastedPosts } from '../../redux/actions/community';
import { push } from 'connected-react-router';
import TextArea from 'antd/lib/input/TextArea';

const { TabPane } = Tabs;
// kick off the polyfill!
smoothscroll.polyfill();

class Community extends Component {
  constructor(props) {
    super(props)
    this.myRef = React.createRef()   // Create a ref object 

    this.state = {
      scrollToTop: false,
      commenting: null,
      tabViewKey: "1",
      loading: true,
      broadcastLoading: false,
      loadingMore: false,
      page: 1,
      commentLoading: false,
      confirmLoading: false,
      selectedPost: null,
      editValue: '',
    }
  }
  componentDidMount() {
    this.myRef.current.addEventListener('scroll', this.listenToScroll)
    new Promise((resolve, reject) => {
      this.props.getAllPosts(this.state.page, resolve, reject)
    }).then((posts) => {
      this.props.setAllPosts(posts);
      this.setState({
        loading: false,
      })
    })
  }
  componentWillUnmount() {
    this.myRef.current.removeEventListener('scroll', this.listenToScroll)
  }
  listenToScroll = () => {
    if (this.myRef.current.scrollTop > 250) {
      this.state.scrollToTop || this.setState({
        scrollToTop: true,
      })
    } else {
      this.state.scrollToTop && this.setState({
        scrollToTop: false,
      })
    }
  }
  scrollToTop = () => {
    this.myRef.current.scroll({ top: 0, left: 0, behavior: 'smooth' });
  }
  onLikeHandler = post => {
    if (!this.props.user.verified) {
      message.error("Please verify you email address before any community activity.");
      return;
    }
    const { likedBy, comments, sender, content, _id, timeStamp } = post;
    likedBy.push(this.props.user._id);

    let updatedPosts = [...this.props.allPosts];
    updatedPosts.find((p, i) => {
      if (p._id === post._id) {
        updatedPosts[i] = { ...p, likedBy: likedBy };
        if (this.state.commenting && post._id === this.state.commenting._id) {
          this.setState({
            commenting: updatedPosts[i],
          })
        }
        return true;
      }
      return false;
    })
    this.props.setAllPosts(updatedPosts);
    this.props.updatePost(_id, {
      sender: sender._id,
      content,
      comments,
      likedBy,
      timeStamp
    })
  }
  onDislikeHandler = (post) => {
    const { likedBy, comments, sender, content, _id, timeStamp } = post;
    let updatedLikers = likedBy.filter(id => id !== this.props.user._id)

    let updatedPosts = [...this.props.allPosts];
    updatedPosts.find((p, i) => {
      if (p._id === post._id) {
        updatedPosts[i] = { ...p, likedBy: updatedLikers };
        if (this.state.commenting && post._id === this.state.commenting._id) {
          this.setState({
            commenting: updatedPosts[i],
          })
        }
        return true;
      }
      return false;
    })
    this.props.setAllPosts(updatedPosts);
    this.props.updatePost(_id, {
      sender: sender._id,
      content,
      comments,
      likedBy: updatedLikers,
      timeStamp
    });
  }
  addPostHandler = (content, status) => {
    return this.props.createPost({
      sender: this.props.user._id,
      timeStamp: Date.now(),
      status: status ? "broadcast" : "post",
      content
    }).then(({ data }) => {
      let updatedPosts = [...this.props.allPosts];
      updatedPosts.unshift(data.data)
      this.props.setAllPosts(updatedPosts);
    })
  }
  filteredPosts = (allPosts) => {
    return allPosts.filter(p => p.sender._id === this.props.user._id);
  }
  addComment = (comment, currPost) => {
    if (!comment.trim()) {
      this.setState({ commentLoading: false });
      return;
    }
    if (!this.props.user.verified) {
      message.error("Please verify you email address before any community activity.");
      this.setState({ commentLoading: false });
      return;
    }
    this.setState({ commentLoading: true });
    const { likedBy, comments, sender, content, _id, timeStamp } = currPost;
    let updatedPosts = [...this.props.allPosts];
    let tempComments = [...comments];

    //for local updating
    tempComments.push({
      user: {
        _id: this.props.user._id,
        username: this.props.user.username,
      },
      text: comment.trim(),
    })
    updatedPosts.find((post, i) => {
      if (post._id === _id) {
        updatedPosts[i] = { ...currPost, comments: tempComments }
        if (this.state.commenting) {
          this.setState({
            commenting: updatedPosts[i],
          })
        }
        return true;
      }
      return false;
    })
    this.props.setAllPosts(updatedPosts);
    //------------------------------
    comments.push({
      user: this.props.user._id,
      text: comment.trim(),
    })
    this.props.updatePost(_id, {
      sender: sender._id,
      content,
      comments,
      likedBy,
      timeStamp
    }).then(() => {
      this.setState({ commentLoading: false });
    })
  }
  deleteCommentHandler = (index) => {
    if (!this.state.commenting) {
      return;
    }
    let currentPost = { ...this.state.commenting };
    let { likedBy, comments, sender, content, _id, timeStamp } = currentPost;
    comments = comments.filter((c, i) => i !== index);
    let updatedPosts = [...this.props.allPosts];
    updatedPosts.find((p, i) => {
      if (p._id === currentPost._id) {
        updatedPosts[i] = { ...currentPost, comments };
        if (this.state.commenting) {
          this.setState({
            commenting: updatedPosts[i],
          })
        }
        return true;
      }
      return false;
    })
    this.props.setAllPosts(updatedPosts);
    this.props.updatePost(_id, {
      sender: sender._id,
      content,
      comments,
      likedBy,
      timeStamp
    })
  }
  onRefresh = () => {
    this.setState({
      loading: true,
      page: 1,
    }, () => {
      new Promise((resolve, reject) => {
        this.props.getAllPosts(this.state.page, resolve, reject)
      }).then((posts) => {
        this.props.setAllPosts(posts);
        this.setState({
          loading: false,
        })
      })
    })
  }
  loadMore = () => {
    this.setState({
      loadingMore: true,
    })
    new Promise((resolve, reject) => {
      this.props.getAllPosts(this.state.page + 1, resolve, reject)
    }).then((posts) => {
      this.props.setAllPosts([...this.props.allPosts, ...posts]);
      this.setState(prevState => ({
        loadingMore: false,
        page: prevState.page + 1
      }))
    })
  }
  deletePostHandler = id => {
    let updatedPosts = this.props.allPosts.filter(p => p._id !== id);
    this.props.setAllPosts(updatedPosts);
    if (this.state.commenting && this.state.commenting._id === id) {
      this.setState({
        commenting: null,
      })
    }
    this.props.deletePost(id);
  }
  handleOk = () => {
    if (!this.state.editValue.trim()) {
      return;
    }
    this.setState({
      confirmLoading: true,
    });

    const { likedBy, comments, sender, _id, timeStamp } = this.state.selectedPost;

    let updatedPosts = [...this.props.allPosts];
    updatedPosts.find((p, i) => {
      if (p._id === _id) {
        updatedPosts[i] = { ...p, content: this.state.editValue };
        if (this.state.commenting && p._id === this.state.commenting._id) {
          this.setState({
            commenting: updatedPosts[i],
          })
        }
        return true;
      }
      return false;
    })
    this.props.updatePost(_id, {
      sender: sender._id,
      content: this.state.editValue,
      comments,
      likedBy,
      timeStamp
    }).then(() => {
      this.props.setAllPosts(updatedPosts);
      this.setState({
        selectedPost: null,
        confirmLoading: false,
      });
    })

  };
  handleCancel = () => {
    this.setState({
      selectedPost: null,
    });
  };
  editingPost = (post) => {
    this.setState({
      selectedPost: post,
      editValue: post.content,
      commenting: null,
    })
  }

  handleTabViewChange = key => {
    this.setState({ tabViewKey: key, page: 1 })
    if (key === "3") {
      if (!this.props.allBroadcastedPosts.length) {
        this.setState({
          broadcastLoading: true,
        })
        this.props.getAllBroadcastedPosts(1).then((posts) => {
          this.props.setAllBroadcastedPosts(posts);
          this.setState({
            broadcastLoading: false,
          })
        })
      }
    }
  }

  render() {
    const {
      selectedPost, confirmLoading, editValue, tabViewKey, scrollToTop, loading, commentLoading, commenting
    } = this.state;
    let filteredPosts = [];
    if (tabViewKey === "2") {
      filteredPosts = this.filteredPosts(this.props.allPosts);
    }

    return (
      <div style={{
        width: '100%',
        height: 'calc(100vh - 64px)',
        overflow: 'auto',
      }} ref={this.myRef}>
        <div className={styles.container}>
          <PostValue
            addHandler={this.addPostHandler}
            loading={loading}
            refresh={this.onRefresh}
            verifiedUser={this.props.user.verified}
            role={this.props.user.role}
          />
          <Tabs onChange={this.handleTabViewChange} activeKey={tabViewKey} tabPosition='left'>
            <TabPane tab="Timeline" key="1">
              {
                this.state.loading ?
                  <div style={{
                    width: '100%',
                    height: 150,
                    display: 'flex',
                    flexDirection: 'column',
                    justifyContent: 'center'
                  }}>
                    <Spin />
                  </div>
                  :
                  this.props.allPosts.length ? this.props.allPosts.sort(function (x, y) {
                    return y.timeStamp - x.timeStamp;
                  }).map((post, i) => (
                    <Post
                      deletePost={() => {
                        let id = post._id;
                        this.deletePostHandler(id);
                      }}
                      editPost={() => {
                        let currPost = post;
                        this.editingPost(currPost);
                      }}
                      broadcast={post.status === 'broadcast'}
                      key={i}
                      postId={post._id}
                      currUser={this.props.user}
                      routeTo={url => this.props.push(url)}
                      userId={post.sender._id}
                      user={post.sender}
                      liked={post.likedBy.includes(this.props.user._id)}
                      timestamp={post.timeStamp}
                      statusText={post.content}
                      likeCount={post.likedBy.length}
                      commentsCount={post.comments.length}
                      onCommentClick={() => {
                        let currPost = post;
                        this.setState({
                          commenting: currPost,
                        });
                      }}
                      onLikeClick={() => {
                        let currPost = post;
                        this.onLikeHandler(currPost);
                      }}
                      onDislikeClick={() => {
                        let currPost = post;
                        this.onDislikeHandler(currPost);
                      }}
                    />
                  )) :
                    <span style={{
                      width: 'fit-content',
                      display: 'block',
                      margin: '50px auto',
                      color: '#00000080'
                    }}>No posts available</span>
              }
              {
                Math.ceil(this.props.postsCount / 10) > this.state.page && !this.state.loading ?
                  <div style={{ textAlign: 'center' }}>
                    <Button loading={this.state.loadingMore} onClick={this.loadMore} type='secondary'>Load More</Button>
                  </div>
                  : null
              }
            </TabPane>
            <TabPane tab="My Posts" key="2">
              {
                this.state.loading ?
                  <div style={{
                    width: '100%',
                    height: 150,
                    display: 'flex',
                    flexDirection: 'column',
                    justifyContent: 'center'
                  }}>
                    <Spin />
                  </div>
                  :
                  filteredPosts.length ? filteredPosts.sort(function (x, y) {
                    return y.timeStamp - x.timeStamp;
                  }).map((post, i) => (
                    <Post
                      key={i}
                      deletePost={() => {
                        let id = post._id;
                        this.deletePostHandler(id);
                      }}
                      editPost={() => {
                        let currPost = post;
                        this.editingPost(currPost);
                      }}
                      postId={post._id}
                      broadcast={post.status === 'broadcast'}
                      currUser={this.props.user}
                      routeTo={url => this.props.push(url)}
                      userId={post.sender._id}
                      user={post.sender}
                      liked={post.likedBy.includes(this.props.user._id)}
                      timestamp={post.timeStamp}
                      statusText={post.content}
                      likeCount={post.likedBy.length}
                      commentsCount={post.comments.length}
                      onCommentClick={() => {
                        let currPost = post;
                        this.setState({
                          commenting: currPost,
                        });
                      }}
                      onLikeClick={() => {
                        let currPost = post;
                        this.onLikeHandler(currPost);
                      }}
                      onDislikeClick={() => {
                        let currPost = post;
                        this.onDislikeHandler(currPost);
                      }}
                    />
                  )) :
                    <span style={{
                      width: 'fit-content',
                      display: 'block',
                      margin: '50px auto',
                      color: '#00000080'
                    }}>No posts available</span>
              }
            </TabPane>
            <TabPane tab="Broadcasted Posts" key="3">
              {
                this.state.broadcastLoading ?
                  <div style={{
                    width: '100%',
                    height: 150,
                    display: 'flex',
                    flexDirection: 'column',
                    justifyContent: 'center'
                  }}>
                    <Spin />
                  </div>
                  :
                  this.props.allBroadcastedPosts.length ? this.props.allBroadcastedPosts.sort(function (x, y) {
                    return y.timeStamp - x.timeStamp;
                  }).map((post, i) => (
                    <Post
                      key={i}
                      deletePost={() => {
                        let id = post._id;
                        this.deletePostHandler(id);
                      }}
                      editPost={() => {
                        let currPost = post;
                        this.editingPost(currPost);
                      }}
                      broadcast={post.status === 'broadcast'}
                      postId={post._id}
                      currUser={this.props.user}
                      routeTo={url => this.props.push(url)}
                      userId={post.sender._id}
                      user={post.sender}
                      liked={post.likedBy.includes(this.props.user._id)}
                      timestamp={post.timeStamp}
                      statusText={post.content}
                      likeCount={post.likedBy.length}
                      commentsCount={post.comments.length}
                      onCommentClick={() => {
                        let currPost = post;
                        this.setState({
                          commenting: currPost,
                        });
                      }}
                      onLikeClick={() => {
                        let currPost = post;
                        this.onLikeHandler(currPost);
                      }}
                      onDislikeClick={() => {
                        let currPost = post;
                        this.onDislikeHandler(currPost);
                      }}
                    />
                  )) :
                    <span style={{
                      width: 'fit-content',
                      display: 'block',
                      margin: '50px auto',
                      color: '#00000080'
                    }}>No posts available</span>
              }
            </TabPane>
          </Tabs>
          <Button
            className={styles.scrollBtn}
            style={{
              display: scrollToTop ? "initial" : 'none',
            }}
            onClick={this.scrollToTop}
          >Top
          </Button>
        </div>
        <CommentDrawer
          editPost={this.editingPost}
          deleteComment={this.deleteCommentHandler}
          onDeletePostHandler={this.deletePostHandler}
          routeTo={url => this.props.push(url)}
          commentLoading={commentLoading}
          addComment={this.addComment}
          commenting={commenting}
          onClose={() => this.setState({ commenting: null })}
          onLikeHandler={this.onLikeHandler}
          onDislikeHandler={this.onDislikeHandler}
          user={this.props.user}
        />
        <Modal
          title="Edit Post"
          visible={Boolean(selectedPost)}
          onOk={this.handleOk}
          okText={'Update'}
          confirmLoading={confirmLoading}
          onCancel={this.handleCancel}
          centered
        >
          <TextArea
            value={editValue}
            style={{
              resize: 'none',
              borderRadius: '0',
              boxShadow: 'none !important',
            }}
            onChange={({ target }) => this.setState({ editValue: target.value })}
            placeholder="What's on your mind?"
            rows={5}
            disabled={confirmLoading}
          />
        </Modal>
      </div>
    )
  }
}

//these functions below are used for redux only!!!

//this is used to get data from redux
const mapStateToProps = state => ({
  allPosts: state.community.allPosts,
  allBroadcastedPosts: state.community.allBroadcastedPosts,
  postsCount: state.community.postsCount,
  user: state.auth.user,
})

//this is used to send/update data to redux
const mapDispatchToProps = dispatch => {
  return bindActionCreators({
    getAllPosts,
    setAllPosts,
    getAllBroadcastedPosts,
    setAllBroadcastedPosts,
    updatePost,
    deletePost,
    createPost,
    push,
  }, dispatch)
}

export default connect(mapStateToProps, mapDispatchToProps)(Community);