import React, { Component } from 'react'
import {
  Form,
  Icon,
  Input,
  Button,
  Checkbox,
  notification,
} from 'antd';
import './Login.scss';
import { theme } from '../../shared/theme';
import logoImg from '../../assets/logo.png';
import { push } from 'connected-react-router';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { signIn } from '../../redux/services/auth'
import logoWideBlue from '../../assets/logoWideBlue.png'
class Login extends Component {

  state = {
    login: false,
    loginLoader: false,
  }

  handleSubmit = (e) => {
    this.setState({ loginLoader: true });
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        new Promise((resolve, reject) => {
          this.props.signIn({
            username: values.userName,
            password: values.password,
          }, resolve, reject)
        }).then(() => {
          this.setState({ loginLoader: false });
          this.props.signedIn();
        }, () => {
          this.props.form.resetFields(['password']);
          this.setState({ loginLoader: false });
          this.openNotification();
        })
      } else {
        this.setState({ loginLoader: false });
      }
    });
  }

  openNotification = () => {
    notification.error({
      message: 'Sorry, your email or password are incorrect - please try again.',
    });
  }

  renderForm = () => {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form onSubmit={this.handleSubmit} className="login-form">
        <img className='medium' style={{ display: 'block', margin: 30, userDrag: 'none' }} src={logoWideBlue} width={300} alt="logo" />
        <h2 className='large h-font'>Login to your account</h2>
        <Form.Item>
          {getFieldDecorator('userName', {
            rules: [{ required: true, message: 'Please input your email!' }],
          })(
            <Input prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />} type='email' autoFocus placeholder="Email" />
          )}
        </Form.Item>
        <Form.Item>
          {getFieldDecorator('password', {
            rules: [{ required: true, message: 'Please input your Password!' }],
          })(
            <Input prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />} type="password" placeholder="Password" />
          )}
        </Form.Item>
        <Form.Item>
          {getFieldDecorator('remember', {
            valuePropName: 'checked',
            initialValue: true,
          })(
            <Checkbox>Remember me</Checkbox>
          )}
          {/* <a className="login-form-forgot" href="/">Forgot password</a> */}
          <Button
            style={{
              background: theme.main,
              color: theme.secondary
            }}
            type="primary"
            htmlType="submit"
            className="login-form-button"
          >
            {this.state.loginLoader ? <Icon type="loading" style={{ width: '38.3px' }} /> : "Log in"}
          </Button>
          <span style={{ textAlign: 'center', display: 'block' }}>
            Don't have an account?
            <span
              className='highlightedLink'
              onClick={() => this.props.push('/register')}
            > Register now!</span>
          </span>
        </Form.Item>
      </Form>
    )
  }

  render() {
    return (
      <div className='login'>
        <div className="desc large">
          <div className="overlay"></div>
          <img src={logoImg} width={220} alt="logo" style={{ zIndex: 150 }} />
          <p style={{ zIndex: 150 }}>
            Karachi University Alumni System (KUAS) is an online application system which can act as a interactive medium between the old student and the University of Karachi. KUAS is a web based application which helps the institutions to track old students and to track them for future endeavours.
          </p>
        </div>
        <div className="loginForm">
          {this.renderForm()}
        </div>
      </div>
    )
  }
}

const mapDispatchToProps = dispatch => {
  return bindActionCreators({
    push,
    signIn
  }, dispatch);
}


export default connect(null, mapDispatchToProps)(Form.create({ name: 'login' })(Login));