import React, { PureComponent } from 'react'
import { Drawer, Input, Button, Avatar, Icon, Modal } from 'antd';
import Post from '../Post/Post';
const { confirm } = Modal;

function Comment({ onDelete, text, user, currUser, index }) {
  return (
    <div
      className="commentItem"
      style={{
        margin: '10px auto',
        padding: 10,
        borderRadius: 4,
        background: '#00000004'
      }}
    >
      <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
        <div style={{ display: 'flex', alignItems: 'center' }}>
          <Avatar src='https://picsum.photos/50' size={25} icon="user" shape=' circle' />
          <h2 style={{ textTransform: 'capitalize', fontSize: 13, margin: 0, marginLeft: 10 }}>{user.username}</h2>
        </div>
        {currUser._id === user._id || currUser.role === 'super-admin' ?
          <Icon onClick={() => onDelete(index)} style={{ color: '#980000', cursor: 'pointer' }} type="delete" />
          : null
        }
      </div>
      <div style={{ marginLeft: 35 }}>{text}</div>
    </div>
  )
}

export default class CommentDrawer extends PureComponent {

  state = {
    value: '',
  }

  showDeleteConfirm = (index) => {
    confirm({
      title: 'Are you sure you want to delete this comment?',
      okText: 'Yes',
      okType: 'danger',
      cancelText: 'No',
      icon: <Icon style={{ color: '#980000', cursor: 'pointer' }} type="delete" />,
      onOk: () => {
        this.props.deleteComment(index)
      },
      onCancel: () => {

      },
    });
  }

  scrollToBottom = () => {
    this.messagesEnd.scrollIntoView({ behavior: "smooth" });
  }

  render() {
    const { routeTo, commentLoading, addComment, user, onDislikeHandler, onClose, commenting, onLikeHandler, onDeletePostHandler, editPost } = this.props;
    let post = null;
    let commentsList = null;
    if (commenting) {
      const { sender, likedBy, timeStamp, content, comments, _id, status } = commenting;
      post = (
        <Post
          editPost={() => {
            editPost(commenting);
          }}
          routeTo={routeTo}
          deletePost={() => {
            let id = _id;
            onDeletePostHandler(id);
          }}
          broadcast={status === 'broadcast'}
          postId={_id}
          currUser={user}
          userId={sender._id}
          user={sender}
          liked={likedBy.includes(user._id)}
          timestamp={timeStamp}
          statusText={content}
          likeCount={likedBy.length}
          commentsCount={comments.length}
          onCommentClick={() => { }}
          onLikeClick={() => onLikeHandler(commenting)}
          onDislikeClick={() => onDislikeHandler(commenting)}
        />
      )
      commentsList = (comments.length ?
        comments.map((c, i) => (
          <Comment key={i} index={i} onDelete={this.showDeleteConfirm} user={c.user} currUser={user} text={c.text} />
        ))
        : <span
          style={{
            display: 'block',
            margin: 'auto',
            width: 'fit-content',
            marginTop: 50,
            color: '#d3d3d3',
            userSelect: 'none'
          }}
        >No comments yet</span>
      )

    }
    return (
      <Drawer
        placement="right"
        closable={true}
        onClose={() => {
          this.setState({ value: '' });
          onClose();
        }}
        visible={Boolean(commenting)}
        width={600}
        bodyStyle={{
          height: '100%',
        }}
      >
        {post}
        <div
          style={{
            marginBottom: 50,
          }}
        >
          <h3>Comments</h3>
          {commentsList}
        </div>
        <div style={{
          height: 50,
          position: 'absolute',
          bottom: 0,
          left: 0,
          width: '100%',
          background: '#eee',
          display: 'flex',
        }}>
          <Input
            type='text'
            value={this.state.value}
            onChange={e => this.setState({ value: e.target.value })}
            onPressEnter={() => {
              if (commentLoading) {
                return;
              }
              addComment(this.state.value, commenting)
              this.setState({ value: '' });
              setTimeout(() => {
                this.scrollToBottom();
              });
            }}
            placeholder='Type your comment'
            style={{
              background: 'transparent',
              border: 'none',
              boxShadow: 'none',
              borderColor: 'transparent',
              lineHeight: '50px',
              height: 50,
            }}
          />
          <Button
            type='primary'
            loading={commentLoading}
            onClick={() => {
              addComment(this.state.value, commenting)
              this.setState({ value: '' });
              setTimeout(() => {
                this.scrollToBottom();
              });
            }}
            style={{
              height: '100%',
              borderRadius: 0,
            }}
          >Comment</Button>
        </div>
        <div
          style={{ float: "left", clear: "both" }}
          ref={(el) => { this.messagesEnd = el; }}
        >
        </div>
      </Drawer>
    )
  }
}