import { request } from '../../../shared/verb';
import { setDashboardStats, setCommunityStats } from '../../actions/dashboard';


export const getDashboardStats = (id) => (dispatch) => {
  return request("stats/dashboard", null, 'get').then(({ data }) => {
    dispatch(setDashboardStats(data.data));
  })
}

export const getCommunityStats = (id) => (dispatch) => {
  return request("stats/community", null, 'get').then(({ data }) => {
    dispatch(setCommunityStats(data.data.lastMonthTrend));
  })
}