import { request } from '../../../shared/verb';
import { setAllAlumni, setPendingAlumni } from '../../actions/alumni';

export const getAllAlumni = () => (dispatch) => {
  return request("alumni/all", null, 'get').then(({ data }) => {
    dispatch(setAllAlumni(data.data));
  })
}

export const getAllPendingAlumni = () => (dispatch) => {
  return request("alumni/pending", null, 'get').then(({ data }) => {
    dispatch(setPendingAlumni(data.data));
  })
}

export const updateAlumni = (id, verificationStatus) => (dispatch) => {
  return request("user/update?id=" + id, {
    status: verificationStatus
  }, 'put');
}