import Alumni from '../Alumni/Alumni'
import HeadOfDepartments from '../HeadOfDepartments/HeadOfDepartments'
import Departments from '../Departments/Departments';
import UserProfile from '../UserProfile/UserProfile';
import Community from '../Community/Community';
import Dashboard from '../Dashboard/Dashboard';
import PendingAlumni from '../PendingAlumni/PendingAlumni';

export default [
  {
    path: '/app',
    name: 'Dashboard',
    component: Dashboard,
    exact: true,
    strict: true
  },
  {
    path: '/app/alumni',
    name: 'Alumni',
    component: Alumni,  //Attached the Alumni component here!
    exact: true,
    strict: true
  },
  {
    path: '/app/pending-alumni',
    name: 'Pending Alumni',
    component: PendingAlumni,
    exact: true,
    strict: true
  },
  {
    path: '/app/departments',
    name: 'Departments',
    component: Departments,
    exact: true,
    strict: true
  },
  {
    path: '/app/hods',
    name: 'Head of Departments',
    component: HeadOfDepartments,
    exact: true,
    strict: true
  },
  {
    path: '/app/community',
    name: 'Community',
    component: Community,
    exact: true,
    strict: true
  }, {
    path: '/app/profile',
    name: 'Profile',
    component: UserProfile,
  },
]