import React from 'react'
import { Avatar, Icon, Tooltip, Menu, Dropdown, Modal } from 'antd';
import Moment from 'react-moment';
import styles from './Post.module.scss';
import moment from 'moment';
const { confirm } = Modal;

const Post = ({ editPost, deletePost, routeTo, userId, currUser, user, timestamp, statusText, likeCount, commentsCount, onLikeClick, onDislikeClick, onCommentClick, liked, broadcast }) => {
  let timeoutFlag = null;

  const likeHandler = () => {
    clearTimeout(timeoutFlag);
    timeoutFlag = setTimeout(() => {
      if (liked) {
        onDislikeClick();
      } else {
        onLikeClick();
      }
    }, 500);
  }

  const showDeleteConfirm = () => {
    confirm({
      title: 'Are you sure you want to delete this post?',
      okText: 'Yes',
      okType: 'danger',
      cancelText: 'No',
      icon: <Icon style={{ color: '#980000', cursor: 'pointer' }} type="delete" />,
      onOk: () => {
        deletePost()
      },
      onCancel: () => {

      },
    });
  }

  let menu = (
    <Menu>
      <Menu.Item onClick={showDeleteConfirm} key="1">
        Delete
  </Menu.Item>
    </Menu>
  );
  if (currUser.role === 'super-admin') {
    menu = (
      <Menu>
        <Menu.Item onClick={showDeleteConfirm} key="1">
          Delete
        </Menu.Item>
      </Menu>
    );
  }
  if (userId === currUser._id) {
    menu = (
      <Menu>
        <Menu.Item onClick={editPost} key="0">
          Edit
        </Menu.Item>
        <Menu.Item onClick={showDeleteConfirm} key="1">
          Delete
        </Menu.Item>
      </Menu>
    );
  }

  return (
    <div className={styles.post}>
      <div className={styles.mainInfo}>
        <div>
          <Avatar src='https://picsum.photos/50' size={30} icon="user" shape=' circle' />
          <div style={{ display: 'flex', flexDirection: 'column', marginLeft: 10 }}>
            <h2 style={{ textTransform: 'capitalize', cursor: 'pointer' }} onClick={() => routeTo('/app/profile/' + user._id)}>{user.username}</h2>
            <span className={styles.timeStamp}>
              <Tooltip placement='right' mouseEnterDelay={1.2} title={<span style={{ fontSize: 10 }}>{moment(timestamp).format('MMM DD, YYYY h:mm a')}</span>}>
                <Moment fromNow date={timestamp} />
              </Tooltip>
            </span>
          </div>
        </div>
        <div>
          {broadcast ?
            <Icon type="notification" style={{
              color: '#f17c15',
              marginBottom: 3,
              fontSize: 16
            }} theme="filled" />
            : null
          }
          <span className={styles.menuIcon} style={{
            display: userId === currUser._id || currUser.role === 'super-admin' ? 'block' : 'none'
          }}>
            <Dropdown overlay={menu} trigger={['click']}>
              <span className="ant-dropdown-link" >
                <Icon style={{ fontSize: 18 }} type="more" />
              </span>
            </Dropdown>
          </span>
        </div>
      </div>
      <div className={styles.postData}>
        {statusText}
      </div>
      <div className={styles.reactionContainer}>
        <div>
          <span style={{ color: liked ? '#e0245e' : null }}>{likeCount ? likeCount : null}</span>
          <Tooltip title={() => <span style={{ fontSize: 11 }}>like</span>} placement='right' mouseEnterDelay={1}>
            <Icon onClick={likeHandler} type="heart" theme={liked ? "filled" : "outlined"} style={{ fontSize: '15px', color: liked ? '#e0245e' : null, marginLeft: 5, cursor: 'pointer' }} />
          </Tooltip>
        </div>
        <div>
          <span>{commentsCount ? commentsCount : null}</span>
          <Tooltip title={() => <span style={{ fontSize: 11 }}>comment</span>} placement='right' mouseEnterDelay={1}>
            <Icon onClick={onCommentClick} type="message" theme="outlined" style={{ fontSize: '15px', marginLeft: 5, cursor: 'pointer' }} />
          </Tooltip>
        </div>
      </div>
    </div>
  )
}

export default React.memo(Post);