import { request } from '../../../shared/verb';
import { setAllHOD } from '../../actions/hod';

export const getAllHOD = () => (dispatch) => {
  return request("hod/all", null, 'get').then(({ data }) => {
    dispatch(setAllHOD(data.data));
  })
}

export const createHOD = (data, prevSource) => (dispatch) => {
  return request("admin/create", data, 'post').then(({ data }) => {
    const newSource = [...prevSource]
    newSource.push(data.data.mongoDB)
    dispatch(setAllHOD(newSource));
  })
}