export const actions = {
  SET_ALL_POSTS: "SET_ALL_POSTS",
  SET_POSTS_COUNT: 'SET_POSTS_COUNT',
  SET_BROADCASTED_POSTS_COUNT: 'SET_BROADCASTED_POSTS_COUNT',
  SET_BROADCASTED_POSTS: 'SET_BROADCASTED_POSTS',
}

export const setAllPosts = data => ({
  type: actions.SET_ALL_POSTS,
  data,
})
export const setPostsCount = data => ({
  type: actions.SET_POSTS_COUNT,
  data,
})

export const setBroadcastPostsCount = data => ({
  type: actions.SET_BROADCASTED_POSTS_COUNT,
  data,
})
export const setAllBroadcastedPosts = data => ({
  type: actions.SET_BROADCASTED_POSTS,
  data,
})