import { request } from '../../../shared/verb';
import { setPostsCount, setBroadcastPostsCount } from '../../actions/community';

export const getAllPosts = (page, resolve = () => { }, reject = () => { }) => dispatch => {
  return request('posts/all?page=' + page, null, 'get').then(({ data }) => {
    dispatch(setPostsCount(data.data.count));
    resolve(data.data.posts);
  }, reject)
}

export const getAllBroadcastedPosts = (page) => dispatch => {
  return request('broadcast/all?page=' + page, null, 'get').then(({ data }) => {
    dispatch(setBroadcastPostsCount(data.data.count));
    return data.data.post;
  });
}

export const createPost = (body) => () => {
  return request('posts/create', body, 'post');
}

export const updatePost = (id, body) => () => {
  return request('posts/update?id=' + id, body, 'put');
}

export const deletePost = (id) => () => {
  return request('posts/delete?id=' + id, null, 'delete');
}