import { request } from '../../../shared/verb.js';

import { setUserToken, setUserData } from "../../actions/auth";


export const logout = () => {
  return (dispatch) => {
    localStorage.clear();
    dispatch({ type: 'LOGOUT' });
  };
};

export const setUser = (token) => {
  localStorage.setItem('token', token.IdToken);
  localStorage.setItem('refreshToken', token.RefreshToken);
  localStorage.setItem('accessToken', token.AccessToken);
}

export const signIn = (data, resolve, reject) => {
  return (dispatch) => {
    request('auth/login', data, 'post')
      .then(({ data }) => {
        dispatch(setUserToken(data.data.TokenContainer.IdToken));
        dispatch(setUserData(data.data.mongoDB[0]));
        setUser(data.data.TokenContainer);
        resolve();
      })
      .catch(reject);
  };
};

export const refreshToken = () => {
  return () => {
    request('auth/refresh', { refreshToken: localStorage.getItem('refreshToken') }, 'put')
      .then(({ data }) => {
        localStorage.setItem('token', data.data.IdToken);
        localStorage.setItem('accessToken', data.data.AccessToken);
      })
  };
};



// Fetches User info.
export const getUser = (resolve = () => { }, reject = () => { }) => {
  return (dispatch) => {
    return request('user/me', null, 'get')
      .then(({ data }) => {
        dispatch(setUserData(data.data));
        resolve(data.cognito);
      }, reject)
  };
};

export const registerUser = (body) => dispatch => {
  return request('auth/register', body, 'post', true);
}

export const changePassword = (body) => dispatch => {
  return request('auth/changePassword', body, 'post');
}

export const verifyUserCode = (body) => dispatch => {
  return request('auth/verify', body, 'post');
}

export const resendUserCode = (body) => dispatch => {
  return request('user/resendCode', body, 'post');
}

