import { request } from '../../../shared/verb';
import { setAllDepartments } from '../../actions/departments';

export const getAllDepartments = () => (dispatch) => {
  return request("department/all", null, 'get').then(({ data }) => {
    dispatch(setAllDepartments(data.data));
  })
}

export const addDepartment = (data, prevSource) => (dispatch) => {
  return request("department/create", data, 'post').then(({ data }) => {
    const newSource = [...prevSource]
    newSource.push(data.data)
    dispatch(setAllDepartments(newSource));
  })
}

export const deleteDepartment = (id) => (dispatch) => {
  return request("department/delete?id=" + id, null, 'delete');
}