import React, { Component } from 'react'
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import styles from "./Departments.module.scss"; //using scss modules here..
import { Input, Spin, Button, Modal, Form, Select, Icon, Menu, Dropdown } from 'antd';
import { getAllDepartments, addDepartment, deleteDepartment } from '../../redux/services/departments';
import { getAllHOD } from '../../redux/services/HOD'
import { push } from 'connected-react-router'

const { confirm } = Modal;
const FormItem = Form.Item;
const { Search } = Input;
const { Option } = Select

const formItemLayout = {
  labelCol: {
    span: 6
  },
  wrapperCol: {
    span: 14
  }
};

const TableHeading = ({ text, modalAction, onSearch }) => (
  <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', padding: '15px' }}>
    <h2 style={{ margin: 0, }}>{text}</h2>
    <div>
      <Button icon='plus' style={{ marginRight: 10 }} type='primary' onClick={() => modalAction('open')}>Add Department</Button>
      <Search
        placeholder="Search"
        onSearch={onSearch}
        style={{ width: 200 }}
      />
    </div>
  </div>
)

class Departments extends Component {

  state = {
    tableHeight: 500,
    loading: true,
    modalOpen: false,
    inviteCheck: false,
    spinner: false,
    searchKey: ''
  }

  showDeleteConfirm = depart => {
    confirm({
      title: 'Are you sure you want to delete this department?',
      okText: 'Yes',
      okType: 'danger',
      cancelText: 'No',
      icon: <Icon style={{ color: '#980000', cursor: 'pointer' }} type="delete" />,
      onOk: () => {
        this.setState({ loading: true });
        this.props.deleteDepartment(depart._id).then(() => {
          this.getData()
        })
      },
      onCancel: () => {

      },
    });
  }

  editDepart = depart => {
    console.log(depart)
  }

  menu = (depart) => (
    <Menu>
      <Menu.Item onClick={() => this.showDeleteConfirm(depart)} key="1">
        Delete
      </Menu.Item>
    </Menu>
  );

  static DepartmentItem = ({ d, style, menu }) => (
    <div key={d._id} className={style} >
      <span>{d.initials} &ndash; {d.name}</span>
      <Dropdown overlay={menu(d)} trigger={['click']}>
        <span className="ant-dropdown-link" >
          <Icon style={{ fontSize: 17, cursor: 'pointer' }} type="more" />
        </span>
      </Dropdown>
    </div>
  )

  static FormInput = ({ label, initialValue, name, message, required, isDisabled, getFieldDecorator }) => {
    return (
      <FormItem label={label}  {...formItemLayout}>
        {getFieldDecorator(name, {
          initialValue,
          rules: [
            {
              required,
              message
            }
          ]
        })(<Input disabled={isDisabled} />)}
      </FormItem>
    )
  }
  static FormDropDown = ({ label, initialValue, name, message, required, isDisabled, getFieldDecorator, renderOptions, onChange }) => {
    return (
      <FormItem label={label}  {...formItemLayout}>
        {getFieldDecorator(name, {
          initialValue,
          rules: [
            {
              required,
              message
            }
          ]
        })(<Select onChange={onChange ? (id) => onChange(id) : null} getPopupContainer={trigger => trigger.parentNode} disabled={isDisabled} >{renderOptions()}</Select>)}
      </FormItem>
    )
  }

  componentDidMount() {
    if (this.props.user.role === 'alumni') {
      this.props.push('/app/community');
    } else {
      this.resizeTableHandler();
      this.getData()
      window.addEventListener('resize', this.resizeTableHandler); // Subcsription to handle table size on resize
    }
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.resizeTableHandler);
  }

  resizeTableHandler = () => {
    this.setState({
      tableHeight: window.innerHeight - 180,
    })
  }

  getData = () => {
    this.setState({ loading: true })
    this.props.getAllDepartments().then(() => {
      this.setState({ loading: false })
    })
  }

  modalAction = (action) => {
    if (action === 'close') {
      this.setState({ modalOpen: false })
    } else {
      this.setState({ modalOpen: true })
    }
  }

  renderUsers = () => {
    if (this.props.allHOD.length !== 0) {
      return this.props.allHOD.map((HOD, id) => {
        return <Option value={HOD._id} key={id}>{HOD.username}</Option>
      })
    } else {
      return []
    }

  }

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        const data = { ...values }
        this.setState({ spinner: true })
        this.props.addDepartment(data, this.props.allDepartments).then(() => this.setState({ spinner: false }, () => this.modalAction('close')))

      }
    });
  };

  filterIt() {
    const searchKey = this.state.searchKey;
    if (!searchKey.trim()) {
      return [...this.props.allDepartments]
    }
    return this.props.allDepartments.filter(obj => Object.keys(obj).some((key) =>
      (
        (key + "" !== 'key') &&
        (key + "" !== 'updatedAt') &&
        (key + "" !== 'createdAt') &&
        (key + "" !== '_id')) ?
        ((obj[key] + "").toLowerCase()).includes(searchKey.toLowerCase()) : null
    ));
  }

  render() {
    let filteredData = this.filterIt();
    const { getFieldDecorator } = this.props.form;
    return (
      <Spin spinning={this.state.loading}>
        <div className={styles.container}>
          <TableHeading text="Departments" modalAction={this.modalAction} onSearch={val => this.setState({ searchKey: val })} />
          {
            this.state.searchKey ?
              <div style={{
                margin: '15px 25px 5px 30px',
                fontSize: 16
              }}>
                Showing result for "{this.state.searchKey}"
              </div>
              : null
          }
          <div style={{
            padding: '10px 15px',
            display: 'flex',
            flexWrap: 'wrap',
            maxHeight: "calc(100% - 65px)",
            overflow: "auto"
          }}>
            {filteredData.length ?
              filteredData.map(d => (
                <Departments.DepartmentItem menu={this.menu} key={d._id} d={d} style={styles.departmentItem} />
              ))
              : <div style={{ textAlign: 'center', color: '#999', marginTop: 80, flex: 1 }}>No Data</div>
            }
          </div>
        </div>
        <Modal title='Add Department' visible={this.state.modalOpen} destroyOnClose onOk={this.handleSubmit} onCancel={() => this.modalAction('close')}>
          <Spin spinning={this.state.spinner}>
            <Form>
              <Departments.FormInput
                label={'Name: '} name="name" initialValue={''}
                getFieldDecorator={getFieldDecorator} message="Required" required={true}
                isDisabled={false}
              />
              <Departments.FormInput
                label={'Initials: '} name="initials" initialValue={''}
                getFieldDecorator={getFieldDecorator} message="Required" required={true}
                isDisabled={false}
              />
            </Form>
          </Spin>
        </Modal>
      </Spin>

    )
  }
}


//Code below is used to connect this component with the redux store.. you can ignore it.. for now! ;)

//this function is use.. to get data from REDUX store..
const mapStateToProps = state => ({
  allDepartments: state.departments.allDepartments,
  user: state.auth.user,
  allHOD: state.hod.allHOD,
})

//this function is used to provide functions to update data in REDUX store..
const mapDispatchToProps = dispatch => {
  return bindActionCreators({
    getAllDepartments,
    getAllHOD,
    push,
    deleteDepartment,
    addDepartment
  }, dispatch)
}

//Both functions above are connected to the component using "connect" function from "react-redux" package.
export default connect(mapStateToProps, mapDispatchToProps)(Form.create()(Departments));