import React, { Component } from 'react'
import { Steps, Button, message, Form, Input, DatePicker, Select, Icon } from 'antd';
import { push } from 'connected-react-router';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
// import moment from 'moment';
import './Register.scss';
import logoImg from '../../assets/logo.png';
import logoWideBlue from '../../assets/logoWideBlue.png'
import { registerUser, verifyUserCode, setUser } from '../../redux/services/auth'

const Option = Select.Option;
const Step = Steps.Step;
const formItemLayout = {
  labelCol: {
    xs: { span: 24 },
    sm: { span: 8 },
    md: { span: 4 },
  },
  wrapperCol: {
    xs: { span: 24 },
    sm: { span: 16 },
    md: { span: 20 },
  },
};

class Register extends Component {
  state = {
    current: 0,
    codeValue: '',
    email: {
      value: '',
    },
    fullName: {
      value: '',
    },
    password: {
      value: '',
    },
    rePassword: {
      value: '',
    },
    enrollmentNo: {
      value: '',
      tip: 'for eg. "SCI/DCS/EP-XXXXX/XXXX"'
    },
    nicNo: {
      value: '',
      tip: '13 digits (without dashes) "4210198765439"'
    },
    DOB: {
      value: '',
      tip: 'DD/MM/YYYY for eg. "01/03/1997"'
    },
    gender: {
      value: undefined,
    },
    registeringLoader: false,
    verifyingCode: false,
    verificationStatus: null
  }

  handleChange = (e, validator, keyName) => {
    this.setState({
      [keyName]: {
        ...validator(e.target.value),
        value: e.target.value,
        tip: this.state[keyName].tip,
      },
    });
  }

  formItem = (label, obj, type, onChange, placeholder, required, autoFocus) => (
    <Form.Item
      required={required}
      label={label}
      {...formItemLayout}
      validateStatus={obj.validateStatus}
      help={obj.errorMsg || obj.tip}
    >
      <Input
        value={obj.value}
        type={type}
        onChange={onChange}
        placeholder={placeholder}
        autoFocus={autoFocus}
      />
    </Form.Item>
  )

  validateFullName(name = '') {
    if (name.trim() !== '') {
      return {
        validateStatus: 'success',
        errorMsg: null,
      };
    }
    return {
      validateStatus: 'error',
      errorMsg: 'Full Name is required!',
    };
  }

  validateEmail(email) {
    //validation here
    var re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    if (re.test(String(email).toLowerCase())) {
      return {
        validateStatus: 'success',
        errorMsg: null,
      };
    }
    return {
      validateStatus: 'error',
      errorMsg: 'Please enter valid email address',
    };
  }

  validatePassword(pwd) {
    var re = /^(?=.*[0-9])[a-zA-Z0-9! @#$%^&*]{8,16}$/;
    if (re.test(String(pwd).toLowerCase())) {
      return {
        validateStatus: 'success',
        errorMsg: null,
      };
    }
    return {
      validateStatus: 'error',
      errorMsg: 'Password must contain min. 8 and max. 16 characters including at least an alphabet and a number',
    };
  }

  validateEnrollment(enr) {
    var re = /^[A-Za-z]{3}\/[A-Za-z]{3}\/EP-[0-9]{3,7}\/[0-9]{4}/;
    if (re.test(String(enr))) {
      return {
        validateStatus: 'success',
        errorMsg: null,
      };
    }
    return {
      validateStatus: 'error',
      errorMsg: 'Enrollment number is invalid "SCI/DCS/EP-XXXXX/XXXX"',
    };
  }

  validateNIC(nic) {
    var re = /(^.{13,13})$/;
    if (re.test(String(nic))) {
      return {
        validateStatus: 'success',
        errorMsg: null,
      };
    }
    return {
      validateStatus: 'error',
      errorMsg: '13 digits (without dashes) "4210198765439"',
    };
  }

  validateDOB(dob) {
    if (dob && dob._d) {
      return {
        validateStatus: 'success',
        errorMsg: null,
      };
    }
    return {
      validateStatus: 'error',
      errorMsg: 'Please enter date of birth',
    };
  }

  validateGender(gender) {
    if (gender) {
      return {
        validateStatus: 'success',
        errorMsg: null,
      };
    }
    return {
      validateStatus: 'error',
      errorMsg: 'Please select your gender',
    };
  }

  validateRePassword = (pwd = '') => {
    if (pwd.trim() !== '' && pwd === this.state.password.value) {
      return {
        validateStatus: 'success',
        errorMsg: null,
      };
    }
    return {
      validateStatus: 'error',
      errorMsg: 'Password does not match!',
    };
  }

  validationStatus = () => {
    switch (this.state.current) {
      case 0:
        return this.checkFirstValidationStatus();
      case 1:
        return this.checkSecondValidationStatus();
      default:
        break;
    }
  }

  checkFirstValidationStatus = () => {
    const { email, fullName, password, rePassword } = this.state;
    this.handleChange({ target: { value: fullName.value } }, this.validateFullName, 'fullName');
    this.handleChange({ target: { value: email.value } }, this.validateEmail, 'email');
    this.handleChange({ target: { value: password.value } }, this.validatePassword, 'password');
    this.handleChange({ target: { value: rePassword.value } }, this.validateRePassword, 'rePassword');
    return email.validateStatus === 'success' && fullName.validateStatus === 'success' && password.validateStatus === 'success' && rePassword.validateStatus === 'success';
  }

  checkSecondValidationStatus = () => {
    const { DOB, enrollmentNo, nicNo, gender } = this.state;
    this.handleChange({ target: { value: enrollmentNo.value } }, this.validateEnrollment, 'enrollmentNo');
    this.handleChange({ target: { value: nicNo.value } }, this.validateNIC, 'nicNo');
    this.setState({
      DOB: {
        ...this.validateDOB(DOB.value),
        value: DOB.value,
        tip: 'DD/MM/YYYY for eg. "01/03/1997"'
      },
    });
    this.setState({
      gender: {
        ...this.validateGender(gender.value),
        value: gender.value,
      },
    });
    return enrollmentNo.validateStatus === 'success' && nicNo.validateStatus === 'success' && DOB.validateStatus === 'success' && gender.validateStatus === 'success';
  }

  accountsForm = () => {
    const { email, fullName, password, rePassword } = this.state;
    return (
      <Form className='form' {...formItemLayout}>
        {this.formItem("Full Name:", fullName, 'text', (e) => this.handleChange(e, this.validateFullName, 'fullName'), "Enter your full name", true, true)}
        {this.formItem("Email:", email, 'email', (e) => this.handleChange(e, this.validateEmail, 'email'), "Enter your email address", true)}
        {this.formItem("Password:", password, 'password', (e) => this.handleChange(e, this.validatePassword, 'password'), "Enter a password", true)}
        {this.formItem("Password (Again):", rePassword, 'password', (e) => this.handleChange(e, this.validateRePassword, 'rePassword'), "Enter your password again", true)}
      </Form >
    )
  }

  personalDetailsForm = () => {
    const { enrollmentNo, nicNo, DOB, gender } = this.state;
    return (
      <Form className='form' {...formItemLayout}>
        {this.formItem("Enrollment No:", enrollmentNo, 'text', (e) => this.handleChange(e, this.validateEnrollment, 'enrollmentNo'), "Enter your enrollment number", true, true)}
        {this.formItem("NIC no:", nicNo, 'number', (e) => this.handleChange(e, this.validateNIC, 'nicNo'), "Enter your NIC number", true)}
        <Form.Item
          required
          label={"Date of Birth :"}
          {...formItemLayout}
          validateStatus={DOB.validateStatus}
          help={DOB.errorMsg || DOB.tip}
        >
          <DatePicker
            format='DD/MM/YYYY'
            value={DOB.value}
            onChange={(e) => {
              this.setState({
                DOB: {
                  ...this.validateDOB(e),
                  value: e,
                  tip: 'DD/MM/YYYY for eg. "01/03/1997"'
                },
              });
            }}
          />
        </Form.Item>
        <Form.Item
          required
          label={"Gender :"}
          {...formItemLayout}
          validateStatus={gender.validateStatus}
          help={gender.errorMsg || gender.tip}
        >
          <Select
            placeholder='Select your gender'
            style={{ width: 170 }}
            optionFilterProp="children"
            value={gender.value}
            onChange={(e) => {
              this.setState({
                gender: {
                  ...this.validateGender(e),
                  value: e,
                },
              });
            }}
          >
            <Option value="male">Male</Option>
            <Option value="female">Female</Option>
            <Option value="other">Others</Option>
          </Select>
        </Form.Item>
      </Form >
    )
  }

  verifyUserCode = () => {
    if (!this.state.codeValue.trim()) {
      return;
    }
    this.setState({
      verifyingCode: true,
    })
    this.props.verifyUserCode({
      id: this.state.registeredUser._id,
      code: +this.state.codeValue,
    }).then(() => {
      message.success('Congratulation! your email has been verified!');
      this.setState({
        verifyingCode: false,
        verificationStatus: 'verified'
      })
      setTimeout(() => {
        window.location.replace('/');
      }, 1500);
    }, () => {
      message.error('Incorrect verification code.');
      this.setState({
        verifyingCode: false,
        verificationStatus: null,
      })
    })
  }

  lastConfirmationStep = () => (
    <div style={{
      textAlign: 'center',
      height: 300,
      paddingTop: 'calc(300px - 185px)',
      boxSizing: 'border-box',
      fontWeight: 'bold',
    }}>
      {
        this.state.registeringLoader ?
          <div><Icon type='loading' /> &nbsp;&nbsp;Loading...</div>
          :
          <>
            <div>Check your email ({this.state.email.value}) for verification code.</div>
            <div style={{ maxWidth: 350, margin: '20px auto', display: 'flex' }}>
              <Input disabled={this.state.verifyingCode} onPressEnter={this.verifyUserCode} value={this.state.codeValue} onChange={e => this.setState({ codeValue: e.target.value })} placeholder="Enter Verification Code" type='text' />
              <Button
                onClick={this.verifyUserCode}
                disabled={this.state.verificationStatus === 'verified' || this.state.verifyingCode}
                type="primary"
                shape="circle"
                icon={
                  !this.state.verificationStatus ?
                    this.state.verifyingCode ? "loading" : "arrow-right"
                    : this.state.verificationStatus === 'verified' ?
                      "check"
                      : "arrow-right"
                }
                style={{
                  width: 35,
                  marginLeft: 10,
                  background: this.state.verificationStatus === 'verified' ? 'greed' : undefined,
                }}
              />
            </div>
          </>
      }
    </div>
  )
  next() {
    const current = this.state.current + 1;
    this.setState({ current });
    if (current === 2) {
      this.setState({ registeringLoader: true });
      this.props.registerUser({
        username: this.state.fullName.value,
        password: this.state.password.value,
        email: this.state.email.value,
        enrollmentNo: this.state.enrollmentNo.value,
        DOB: this.state.DOB.value,
        gender: this.state.gender.value,
        nicNo: this.state.nicNo.value,
        role: "alumni",
      }).then(({ data }) => {
        let { mongoDB, TokenContainer: tokenContainer } = data.data;
        let { IdToken, AccessToken, RefreshToken } = tokenContainer;
        this.setState({
          registeredUser: mongoDB
        })
        localStorage.setItem('token', IdToken);
        localStorage.setItem('refreshToken', RefreshToken);
        localStorage.setItem('accessToken', AccessToken);
        this.setState({ registeringLoader: false });
      }, ({ response }) => {
        this.setState({ registeringLoader: false });
        message.error(response.data.message)
        this.prev();
      })
    }
  }

  prev() {
    const current = this.state.current - 1;
    this.setState({ current });
  }

  renderSteps = () => {
    const { current } = this.state;
    return (
      <div className='stepsRegister'>
        <Steps current={current}>
          {this.steps.map(item => <Step key={item.title} title={item.title} />)}
        </Steps>
        <div className="steps-content">{this.steps[current].content}</div>
        <div className="steps-action">
          <div>
            {
              current < this.steps.length - 1
              && <Button
                type="primary"
                onClick={() => {
                  if (this.validationStatus()) {
                    this.next();
                  } else {
                    message.warn("Please resolve all errors!");
                  }
                }}
              >Next</Button>
            }
            {
              current === 1
              && (
                <Button style={{ marginLeft: 8 }} onClick={() => this.prev()}>
                  Previous
              </Button>
              )
            }
          </div>
          <span style={{ textAlign: 'center', display: 'block' }}>
            Already have an account?
            <span
              className='highlightedLink'
              onClick={() => this.props.push('/')}
            > Login here!</span>
          </span>
        </div>
      </div >
    )
  }

  render() {
    this.steps = [{
      title: 'Account',
      content: this.accountsForm(),
    }, {
      title: 'Personal Details',
      content: this.personalDetailsForm(),
    }, {
      title: 'Confirmation',
      content: this.lastConfirmationStep(),
    }]

    return (
      <div className='register'>
        <div className="main">
          <img className='medium' style={{ display: 'block', margin: 'auto' }} src={logoWideBlue} width={300} alt="logo" />
          <h2 className='h-font'>Register your new account</h2>
          {this.renderSteps()}
        </div>
        <div className="sideBar large">
          <img src={logoImg} width={150} alt="logo" />
          <p>
            Karachi University Alumni System (KUAS) is an online application system which can act as a interactive medium between the old student and the University of Karachi. KUAS is a web based application which helps the institutions to track old students and to track them for future endeavours.
          </p>
        </div>
      </div>
    )
  }
}


const mapDispatchToProps = dispatch => {
  return bindActionCreators({ push, setUser, registerUser, verifyUserCode }, dispatch);
}

export default connect(null, mapDispatchToProps)(Register);