import React, { useState } from 'react'
import { Input, Button, Icon, message, Checkbox } from 'antd';
const { TextArea } = Input;

export default React.memo(function PostValue({ verifiedUser, addHandler, loading, refresh, role }) {
  const topBarStyle = {
    padding: '5px 10px',
    background: '#f5f6f7',
    border: '1px solid #d9d9d9',
    borderBottom: '1px solid #dddfe2',
    borderRadius: '3px 3px 0 0',
    fontWeight: 'bold',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between',
  }

  const [value, setValue] = useState('')
  const [broadcast, setBroadcast] = useState(false)
  const [loader, setLoader] = useState(false)
  const [dropOpen, setDropOpen] = useState(false)

  const onPostClick = () => {
    if (value.trim()) {
      setLoader(true);
      if (!verifiedUser) {
        message.error("Please verify you email address before creating community post.");
        setLoader(false);
        return;
      }
      addHandler(value, broadcast).then(() => {
        setLoader(false);
        setValue('');
      })
    }
  }

  return (
    <div
      style={{
        marginBottom: 25,
        marginLeft: 160,
      }}
    >
      <div
        style={topBarStyle}
      >
        <span>Create Post</span>
        <Icon onClick={() => loading || refresh()} type={loading ? "loading" : "reload"} />
      </div>
      <TextArea
        onFocus={() => setDropOpen(true)}
        onBlur={() => setDropOpen(false)}
        value={value}
        style={{
          resize: 'none',
          borderRadius: '0',
          boxShadow: 'none !important',
        }}
        onChange={(e) => setValue(e.target.value)}
        placeholder="What's on your mind?"
        rows={3}
        disabled={loader}
      />
      <div
        style={{
          ...topBarStyle,
          background: '#f5f6f7',
          border: '1px solid #d9d9d9',
          borderTop: '1px solid #dddfe2',
          borderRadius: '0 0 3px 3px',
          height: dropOpen || value ? 'auto' : 0,
          padding: dropOpen || value ? '5px 10px' : 0,
        }}
      >
        {role === "super-admin" || role === "Admin" ?
          <Checkbox
            onChange={e => setBroadcast(e.target.checked)}
            checked={broadcast}
            style={{
              display: dropOpen || value ? 'block' : 'none',
            }}
          >Broadcast</Checkbox>
          : null
        }
        <Button
          type="primary"
          disabled={loader || loading}
          style={{
            marginLeft: 'auto',
            display: dropOpen || value ? 'block' : 'none',
            fontSize: 12,
            height: 'auto',
            padding: '4px 10px',
            fontWeight: 500,
          }}
          onClick={onPostClick}
        >
          Post
          {loader ?
            <Icon type="loading" />
            :
            <Icon type="right" />
          }
        </Button>
      </div>
    </div >
  )
});
