import { actions } from "../../actions/dashboard";

const initialState = {
  stats: { alumni: 0, admins: 0, faculty: 0, departs: 0, postsCount: 0, },
  communityStats: null
}

export default (state = initialState, { type, data }) => {
  switch (type) {

    case actions.SET_DASHBOARD_STATS:
      return { ...state, stats: data }
    case actions.SET_COMMUNITY_STATS:
      return { ...state, communityStats: data }
    case 'logout':
      return { ...initialState }
    default:
      return state
  }
}
