import { actions } from '../../actions/community';

const INITIAL_STATE = {
  allPosts: [],
  allBroadcastedPosts: [],
  broadcastedPostsCount: 0,
  postsCount: 0,
};

export default function reducer(state = INITIAL_STATE, { data, type }) {
  switch (type) {
    case actions.SET_ALL_POSTS: {
      return { ...state, allPosts: data };
    }
    case actions.SET_POSTS_COUNT: {
      return { ...state, postsCount: data };
    }
    case actions.SET_BROADCASTED_POSTS_COUNT: {
      return { ...state, broadcastedPostsCount: data };
    }
    case actions.SET_BROADCASTED_POSTS: {
      return { ...state, allBroadcastedPosts: data };
    }
    case "LOGOUT": {
      return { ...INITIAL_STATE };
    }
    default:
      return state;
  }
}