import React, { Component } from 'react'
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import styles from './PendingAlumni.module.scss'
import { Input, Modal, Button, Icon, Spin } from 'antd';
import { getAllPendingAlumni, updateAlumni } from '../../redux/services/alumni';
import { push } from 'connected-react-router'

const Search = Input.Search;
const { confirm } = Modal;

function InfoItem({ title, value }) {
  return (
    <div style={{ display: 'flex' }}>
      <span className='title' style={{ flex: 1, color: '#001529', }}>{title}:</span>
      <span className='value' >{value || <>&ndash;</>}</span>
    </div>
  )
}

const Header = ({ text, modalAction, onSearch = () => { } }) => (
  <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', padding: '15px' }}>
    <h2 style={{ margin: 0, }}>{text}</h2>
    <div>
      <Search
        placeholder="Search"
        onSearch={onSearch}
        style={{ width: 200 }}
      />
    </div>
  </div>
)

class PendingAlumni extends Component {

  state = {
    selectedAlumni: null,
    loading: true,
  }

  componentDidMount() {
    if (this.props.user.role === 'alumni') {
      this.props.push('/app/community');
    } else {
      this.props.getAllPendingAlumni().then(() => {
        this.setState({ loading: false })
      })
    }
  }

  handleVerify = () => {
    confirm({
      title: 'Are you sure you want to verify this alumni?',
      okText: 'Yes',
      okType: 'primary',
      cancelText: 'No',
      icon: <Icon type="done" />,
      onOk: () => {
        this.setState({ loading: true })
        this.props.updateAlumni(this.state.selectedAlumni._id, 'verified').then(() => {
          this.props.getAllPendingAlumni().then(() => {
            this.setState({ loading: false })
          })
        })
        this.setState({ selectedAlumni: null })
      },
      onCancel: () => {

      }
    })
  }
  handleReject = () => {
    confirm({
      title: 'Are you sure you want to reject this alumni?',
      okText: 'Yes',
      okType: 'danger',
      cancelText: 'No',
      icon: <Icon type="close" />,
      onOk: () => {
        this.setState({ loading: true })
        this.props.updateAlumni(this.state.selectedAlumni._id, 'rejected').then(() => {
          this.props.getAllPendingAlumni().then(() => {
            this.setState({ loading: false })
          })
        })
        this.setState({ selectedAlumni: null })
      },
      onCancel: () => {

      }
    })
  }

  render() {
    return (
      <div className={styles.container} >
        <Spin spinning={this.state.loading}>
          <Header text='Pending Alumni' />
          <div style={{
            padding: '10px 15px',
            display: 'flex',
            flexWrap: 'wrap',
            maxHeight: "calc(100% - 65px)",
            overflow: "auto"
          }}>
            {this.props.pendingAlumni.length ?
              this.props.pendingAlumni.map(a => (
                <div key={a._id} onClick={() => this.setState({ selectedAlumni: { ...a } })} className={styles.pendingAlumni}>
                  <div>{a.username}</div>
                  <span>{a.email}</span>
                </div>
              ))
              : <div style={{ textAlign: 'center', color: '#999', marginTop: 80, flex: 1 }}>No Data</div>
            }
          </div>
          <Modal
            title="Verify Alumni"
            visible={Boolean(this.state.selectedAlumni)}
            onOk={this.handleVerify}
            okText={'Verify'}
            destroyOnClose
            onCancel={() => this.setState({ selectedAlumni: null })}
            centered
            footer={[
              <Button key="back" onClick={() => this.setState({ selectedAlumni: null })}>
                Cancel
            </Button>,
              <Button type='danger' key="reject" onClick={this.handleReject}>
                Reject
            </Button>,
              <Button key="submit" type="primary" onClick={this.handleVerify}>
                Verify
            </Button>,
            ]}
          >
            {this.state.selectedAlumni ?
              <>
                <InfoItem title="Name" value={this.state.selectedAlumni.username} />
                <InfoItem title="NIC No" value={this.state.selectedAlumni.nicNo} />
                <InfoItem title="Enrollment No" value={this.state.selectedAlumni.enrollmentNo} />
                <InfoItem title="Department" value={this.state.selectedAlumni.department ? `${this.state.selectedAlumni.department.initials}-${this.state.selectedAlumni.department.name}`.toUpperCase() : undefined} />
                <InfoItem title="Degree" value={this.state.selectedAlumni.degree} />
              </>
              : null
            }
          </Modal>
        </Spin>
      </div>
    )
  }
}

//these functions below are used for redux only!!!

//this is used to get data from redux
const mapStateToProps = state => ({
  user: state.auth.user,
  pendingAlumni: state.alumni.pendingAlumni,
})

//this is used to send/update data to redux
const mapDispatchToProps = dispatch => {
  return bindActionCreators({
    getAllPendingAlumni,
    push,
    updateAlumni,
  }, dispatch)
}


export default connect(mapStateToProps, mapDispatchToProps)(PendingAlumni);